import {
  save_biodata,
  save_bankdata,
  save_userdata,
  save_invoice,
  add_item,
  delete_item,
  add_transactions
} from "./actionTypes";

export const saveBiodata = data => {
  return {
    type: save_biodata,
    payload: data
  };
};

export const saveBankdata = data => {
  return {
    type: save_bankdata,
    payload: data
  };
};
export const saveUserdata = data => {
  return {
    type: save_userdata,
    payload: data
  };
};
export const saveInvoice = data => {
  return {
    type: save_invoice,
    payload: data
  };
};
export const addInvoiceItem = (item, uid, status) => {
  return {
    type: add_item,
    payload: { item, uid, status }
  };
};
export const deleteInvoiceItem = item => {
  return {
    type: delete_item,
    payload: item
  };
};
export const addTransactions = transactions => {
  return {
    type: add_transactions,
    payload: transactions
  };
};
