import {
  save_biodata,
  save_bankdata,
  save_userdata,
  save_invoice,
  add_item,
  delete_item,
  add_transactions
} from "../actions/actionTypes";

const initialState = {
  biodata: null,
  bankdata: null,
  userdata: null,
  invoice: null,
  transactions: null,
  invoiceItem: []
};

export default (state = initialState, action) => {
  switch (action.type) {
    case save_biodata:
      return {
        ...state,
        biodata: action.payload
      };
    case save_bankdata:
      return {
        ...state,
        bankdata: action.payload
      };
    case save_userdata:
      return {
        ...state,
        userdata: action.payload
      };
    case save_invoice:
      return {
        ...state,
        invoice: action.payload
      };
    case add_item:
      //filter state, remove obj with uid action.payload.uid
      let newArray = [];
      if (
        action.payload.status === "edit" ||
        action.payload.status === "update"
      ) {
        if (action.payload.status === "update") {
          newArray = state.invoiceItem;
        } else {
          newArray = state.invoiceItem.filter(
            invItm => invItm.uid != action.payload.uid
          );
        }
      }
      return {
        ...state,
        invoiceItem: [...newArray, action.payload.item]
      };
    case delete_item:
      return {
        ...state,
        invoiceItem: action.payload
      };
    case add_transactions:
      return {
        ...state,
        transactions: action.payload
      };

    default:
      return state;
  }
};
