import React, { useEffect, useState } from "react";
import Loading from "../Loading";
import { connect } from "react-redux";
import { useHistory } from "react-router-dom";
import Cookies from "js-cookie";
import axios from "axios";
import { state_and_lga } from "../../assets/states_and_lga";

//Actions
import { saveBiodata, saveUserdata } from "../../actions/userActions";

import ChangePasswordModal from "./ChangePasswordModal";
const Business = ({ user, runSaveBioData, runSaveUserData }) => {
  const [lgas, setLgas] = useState(["City"]);
  const [fetchUserdata, setFetchUserdata] = useState(false);
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState([]);
  const [successmsg, setSuccessmsg] = useState(null);
  const [formdata, setFormdata] = useState({
    businessname: "",
    address: "",
    city: "",
    state: "",
    sector: "",
    description: ""
  });

  let history = useHistory();
  const getProfile = async token => {
    try {
      const hasProfile = await axios.get("/account/get/biodata", {
        headers: { "X-AUTH-TOKEN": token }
      });

      console.log(hasProfile.data);
      if (!hasProfile.data.status) {
        history.push("/dashboard/setup-biodata");
      } else {
        setFormdata({
          ...formdata,
          address: hasProfile.data.biodata.address,
          city: hasProfile.data.biodata.city,
          state: hasProfile.data.biodata.state,
          sector: hasProfile.data.biodata.sector || "",
          description: hasProfile.data.biodata.description || ""
        });
        //set biodata to state via redux
        runSaveBioData(hasProfile.data);
        setFetchUserdata(true);
      }
    } catch (err) {
      //retry
      getProfile(token);
    }
  };

  const getUserDetails = async token => {
    try {
      const userDetails = await axios.get("/account/get/user", {
        headers: { "X-AUTH-TOKEN": token }
      });
      if (!userDetails.data.status) {
        history.push("/logout");
      } else {
        setFormdata({
          ...formdata,
          businessname: userDetails.data.userdetails.name
        });
        runSaveUserData(userDetails.data);
      }
    } catch (err) {
      //retry
      getUserDetails(token);
    }
  };

  const handleSubmit = async e => {
    e.preventDefault();
    setLoading(true);
    console.log(formdata);
    try {
      let token = Cookies.get("TOKEN");
      const resp = await axios.post("/account/edit/businessdata", formdata, {
        headers: { "X-AUTH-TOKEN": token }
      });
      setLoading(false);
      let { errors, status } = resp.data;
      if (errors) {
        setErrors(errors);
        return;
      } else if (status);
      {
        setSuccessmsg(resp.data.msg);
      }
    } catch (err) {
      setLoading(false);
      setErrors([{ msg: "An error occured with the server, Try again." }]);
    }
  };
  const handleChange = e => {
    e.preventDefault();
    setFormdata({ ...formdata, [e.target.name]: e.target.value });
    if (e.target.id === "state") {
      state_and_lga.map(state => {
        if (state.state === e.target.value) {
          setLgas(state.lgas);
        }
      });
    }
  };

  const runFetchUserData = () => {
    let token = Cookies.get("TOKEN");
    //get and save user details
    if (user.userdata === null) {
      getUserDetails(token);
    } else {
      setFormdata({
        ...formdata,
        businessname: user.userdata.userdetails.name
      });
    }
    setFetchUserdata(false);
  };
  if (fetchUserdata) runFetchUserData();

  useEffect(() => {
    if (Cookies.get("TOKEN") !== null) {
      let token = Cookies.get("TOKEN");

      if (typeof token !== "undefined" && token !== null) {
        //has User created a profile?
        if (user.biodata === null) {
          getProfile(token);
        } else {
          setFormdata({
            ...formdata,
            address: user.biodata.biodata.address,
            city: user.biodata.biodata.city,
            state: user.biodata.biodata.state,
            sector: user.biodata.biodata.sector || "",
            description: user.biodata.biodata.description || ""
          });
          setFetchUserdata(true);
        }
      }
    }
  }, []);
  return (
    <>
      <div className="row">
        <div className="col-md-12 col-lg-12 col-xl-12">
          <div className="card m-b-30">
            <div className="card-body">
              <div
                className="invoice-top"
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "flex-start"
                }}
              >
                <h5 className="header-title pb-3 mt-0">Business Information</h5>
              </div>
              <hr />
              <div id="multi-line-chart" style={{ minHeight: 400 }}>
                {user.userdata ? (
                  <div className="personal-info-form">
                    {/* {user.userdata.userdetails.name} */}
                    <form name="personal" onSubmit={handleSubmit}>
                      {errors.length > 0 ? (
                        <div
                          className="messages  alert alert-danger"
                          style={{
                            padding: "20px"
                          }}
                        >
                          {errors.map((error, key) => (
                            <div key={key}>{error.msg}</div>
                          ))}
                        </div>
                      ) : null}

                      {successmsg ? (
                        <div
                          className="messages alert alert-success"
                          style={{
                            padding: "20px"
                          }}
                        >
                          <div>{successmsg}</div>
                        </div>
                      ) : null}

                      <div className="form-group" style={{ display: "flex" }}>
                        <div className="col-sm-6">
                          <label htmlFor="businessname">Business name</label>
                          <input
                            type="text"
                            id="businessname"
                            className="form-control"
                            name="businessname"
                            value={formdata.businessname}
                            onChange={handleChange}
                            required
                          />
                        </div>
                        <div className="col-sm-6">
                          <label htmlFor="sector">Sector</label>
                          <select
                            className="form-control"
                            id="sector"
                            name="sector"
                            value={formdata.sector}
                            onChange={handleChange}
                          >
                            <option value="" readOnly>
                              Choose one
                            </option>
                            <option value="Transportation">
                              {" "}
                              Transportation
                            </option>
                            <option value="Agriculture"> Agriculture</option>
                            <option value="Transportation">
                              {" "}
                              Transportation
                            </option>
                            <option value="Education"> Education</option>
                            <option value="Trade"> Trade</option>
                            <option value="Health Care"> Health Care</option>
                            <option value="Consumer Goods">
                              Consumer Goods
                            </option>
                            <option value="Hospitality"> Hospitality</option>
                            <option value="Real Estate"> Real Estate</option>
                            <option value="Oil &amp; Gas">
                              {" "}
                              Oil &amp; Gas
                            </option>
                            <option value="Mining"> Mining</option>
                            <option value="Tourism"> Tourism</option>
                            <option value="ICT"> ICT</option>
                            <option value="Power"> Power</option>
                            <option value="Construction"> Construction</option>
                            <option value="Entertainment">Entertainment</option>
                            <option value="Services"> Services</option>
                          </select>
                        </div>
                      </div>
                      <div className="clearfix"></div>
                      <div className="form-group" style={{ display: "flex" }}>
                        <div className="col-sm-6">
                          <label htmlFor="state">State</label>
                          <select
                            className="form-control"
                            id="state"
                            name="state"
                            value={formdata.state}
                            onChange={handleChange}
                            required
                          >
                            <option read-only="read-only">
                              Where is your business located?
                            </option>
                            {state_and_lga.map((state, key) => (
                              <option value={state.state} key={key}>
                                {state.state}
                              </option>
                            ))}
                          </select>
                        </div>
                        <div className="col-sm-6">
                          <label htmlFor="city">City</label>
                          <select
                            className="form-control"
                            id="city"
                            name="city"
                            value={formdata.city}
                            onChange={handleChange}
                            required
                          >
                            {lgas.map((lga, key) => (
                              <option value={lga} key={key}>
                                {lga}
                              </option>
                            ))}
                          </select>
                        </div>
                      </div>
                      <div className="clearfix"></div>
                      <div className="from-group">
                        <div className="col-sm-12">
                          <label htmlFor="address">Address</label>
                          <input
                            type="text"
                            id="address"
                            name="address"
                            className="form-control"
                            value={formdata.address}
                            onChange={handleChange}
                          />
                        </div>
                      </div>
                      <div className="form-group">
                        <div className="col-sm-12">
                          <label htmlFor="description">Description</label>
                          <textarea
                            name="description"
                            className="form-control"
                            onChange={handleChange}
                            value={formdata.description}
                            id="description"
                            cols="30"
                            rows="5"
                          ></textarea>
                        </div>
                      </div>
                      <div className="clearfix"></div>
                      <button
                        type="submit"
                        className="btn float-right btn-theme btn-sm text-uppercase ml-2 pt30 pb30"
                        disabled={loading}
                      >
                        {!loading ? "Save" : "Saving..."}
                        <div
                          className="lds-ring"
                          style={{ display: loading ? "inline-block" : "none" }}
                        >
                          <div></div>
                          <div></div>
                          <div></div>
                          <div></div>
                        </div>
                      </button>
                      <div className="clearfix"></div>
                    </form>
                  </div>
                ) : (
                  <Loading />
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

const mapStateToProps = state => ({
  user: state.user
});
const mapDispatchToProps = dispatch => ({
  runSaveBioData: data => {
    dispatch(saveBiodata(data));
  },
  runSaveUserData: data => {
    dispatch(saveUserdata(data));
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(Business);
