import React, { useState, useEffect } from "react";
import Layout from "../layouts/Dashboard";
import { connect } from "react-redux";
import Breadcrumb from "./Breadcrumb";
import axios from "axios";
import Cookies from "js-cookie";
import Loading from "../Loading";
import { useHistory } from "react-router-dom";

import { addTransactions } from "../../actions/userActions";

const Transactions = ({ user, runAddTransactions }) => {
  let history = useHistory();
  const [transactions, setTransactions] = useState(null);
  const [errors, setErrors] = useState([]);
  const [transactionCount, setTransactionCount] = useState(0);

  const prettyCurrency = amount => {
    const formatter = new Intl.NumberFormat("en-NG", {
      style: "currency",
      currency: "NGN",
      minimumFractionDigits: 2
    });
    const convertedAmount = formatter.format(amount);
    return convertedAmount;
  };

  const getTransactions = async token => {
    try {
      const getAllTransactions = await axios.get(
        "/account/get/transactions/all",
        {
          headers: { "X-AUTH-TOKEN": token }
        }
      );
      if (getAllTransactions.data.errors) {
        setErrors(getAllTransactions.data.errors);
      } else if (
        getAllTransactions.data.transactions &&
        getAllTransactions.data.status
      ) {
        setTransactions(getAllTransactions.data.transactions);
        setTransactionCount(getAllTransactions.data.transactions.length);
        runAddTransactions(getAllTransactions.data.transactions);
      }
    } catch (err) {
      getTransactions(token);
    }
  };

  const openTransaction = txref => {
    history.push(`/dashboard/transactions/${txref}`);
  };

  useEffect(() => {
    if (Cookies.get("TOKEN") !== null) {
      let token = Cookies.get("TOKEN");
      if (typeof token !== "undefined" && token !== null) {
        if (user.transactions) {
          setTransactions(user.transactions);
          setTransactionCount(user.transactions.length);
        } else {
          //get all transactions
          getTransactions(token);
        }
      }
    }
  }, []);
  return (
    <Layout>
      <Breadcrumb primary="Transactions" secondary="All Transactions" />

      <div className="row">
        <div className="col-md-12 col-lg-12 col-xl-12">
          <div className="card m-b-30">
            <div className="card-body">
              <div
                className="invoice-top"
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "flex-start"
                }}
              >
                <h5 className="header-title pb-3 mt-0">
                  {transactionCount > 1
                    ? transactionCount + " Transactions"
                    : transactionCount + " Transaction"}
                </h5>
                <button className="btn btn-sm btn-theme">
                  <i className="mdi mdi-file-pdf"></i>
                  Download PDF
                </button>
              </div>
              <div id="multi-line-chart" style={{ minHeight: 400 }}>
              

                {transactions ? (
                  <div className="table-responsive">
                    <table className="table table-hover custom-table">
                      <thead className="thead-light">
                        <tr style={{ fontWeight: 600 }}>
                          <th>Status</th>
                          <th>Reference</th>
                          <th>Transaction Information</th>
                          <th>Type</th>
                          <th>Channel</th>
                          <th>Date</th>
                        </tr>
                      </thead>

                      <tbody>
                        {transactions.map((trnx, key) => (
                          <tr
                            key={key}
                            onClick={() => openTransaction(trnx.txref)}
                            style={{ cursor: "pointer" }}
                          >
                            <td>
                              <div
                                style={{
                                  backgroundColor:
                                    trnx.status === "successful"
                                      ? "#03d665"
                                      : "#f44336",
                                  width: 25,
                                  height: 25,
                                  color: "#fff",
                                  display: "flex",
                                  alignItems: "center",
                                  justifyContent: "center",
                                  textAlign: "center",
                                  borderRadius: "50%",
                                  margin: "auto"
                                }}
                              >
                                {trnx.status === "successful" ? (
                                  <i
                                    style={{ color: "#fff" }}
                                    className="mdi mdi-check"
                                  ></i>
                                ) : (
                                  <i
                                    style={{ color: "#fff" }}
                                    className="mdi mdi-close"
                                  ></i>
                                )}
                              </div>
                            </td>
                            <td>{trnx.txref}</td>
                            <td>
                              <b>{`${prettyCurrency(trnx.amount)}`}</b> from{" "}
                              {`${trnx.customerEmail}`}
                            </td>
                            <td>{trnx.txtype}</td>
                            <td>
                              <span
                                style={{
                                  padding: ".3rem 0.6rem",
                                  borderRadius: "1rem"
                                }}
                                className={
                                  trnx.paymenttype === "card"
                                    ? "badge badge-info"
                                    : "badge badge-primary"
                                }
                              >
                                {trnx.paymenttype}
                              </span>
                            </td>
                            <td>{new Date(trnx.created).toDateString()}</td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                ) : (
                  <Loading />
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

const mapStateToProps = state => ({
  user: state.user
});
const mapDispatchToProps = dispatch => ({
  runAddTransactions: data => {
    dispatch(addTransactions(data));
  }
});
export default connect(mapStateToProps, mapDispatchToProps)(Transactions);
