import React, { useState, useEffect } from "react";
import axios from "axios";
import Loading from "../Loading";
import { useHistory } from "react-router-dom";
import Cookies from "js-cookie";

export default function AddBankAccountModal({ closeModal, allBanksArray }) {
  let history = useHistory();
  const [isloading, setIsloading] = useState(false);
  const [successmsg, setSuccessmsg] = useState(null);
  const [errors, setErrors] = useState([]);
  const [formdata, setFormdata] = useState({
    accountname: "",
    accountnumber: "",
    bankcode: "",
    isprimary: false
  });

  const getAccountName = async (acctnum, bankcode) => {
    try {
      const accountDetails = await axios.post("/account/verifyaccount", {
        accountnumber: acctnum,
        bankcode: bankcode
      });
      if (accountDetails.data.data.data.responsecode == 0) {
        setFormdata({
          ...formdata,
          bankcode,
          accountnumber: acctnum,
          accountname: accountDetails.data.data.data.accountname
        });
      } else {
        setErrors([{ msg: accountDetails.data.data.data.responsemessage }]);
        setFormdata({
          ...formdata,
          bankcode,
          accountnumber: acctnum,
          accountname: ""
        });
      }
    } catch (err) {
      setErrors([{ msg: "Unable to fetch Account name... Reload the page" }]);
    }
  };

  const handleChange = async e => {
    e.preventDefault();
    setErrors([]);
    setFormdata({ ...formdata, [e.target.name]: e.target.value });
    if (e.target.name == "accountnumber" && e.target.value.length == 10) {
      let acctnum = e.target.value;
      setFormdata({
        ...formdata,
        accountnumber: acctnum,
        accountname: "Fetching Account name... Please wait"
      });
      getAccountName(acctnum, formdata.bankcode);
    } else if (
      e.target.name == "bankcode" &&
      formdata.accountnumber.length == 10
    ) {
      setFormdata({
        ...formdata,
        bankcode: e.target.value,
        accountname: "Fetching Account name... Please wait"
      });
      getAccountName(formdata.accountnumber, e.target.value);
    }
  };
  const handleCheckboxChange = e => {
    // e.preventDefault();
    let box = document.getElementById("checkboxinput");
    setFormdata({ ...formdata, isprimary: box.checked });
  };
  const handleSubmit = async e => {
    e.preventDefault();
    setErrors([]);
    setIsloading(true);
    //get data
    //validation

    try {
      let token = Cookies.get("TOKEN");
      //send data to API
      console.log(formdata);
      const response = await axios.post("/account/create/bank", formdata, {
        headers: { "X-AUTH-TOKEN": token }
      });
      setIsloading(false);
      console.log(response.data);
      let { errors, data } = response.data;
      if (errors) {
        setErrors(errors);
        return;
      } else if (data !== "" && data !== null);
      {
        window.location.reload();
        document.getElementById("closemodal").click();
      }
    } catch (err) {
      setIsloading(false);
      setErrors([{ msg: "An error occured with the server, Try again." }]);
    }
  };

  return (
    <>
      <div
        className="modal fade show bs-example-modal-center"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="mySmallModalLabel"
        aria-hidden="true"
        style={{ display: "block", paddingRight: 17 }}
      >
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <form className="form">
              <div className="modal-header">
                <h5 className="modal-title" id="exampleModalLabel">
                  Add Account
                </h5>
                <button
                  type="button"
                  className="close"
                  id="closemodal"
                  data-dismiss="modal"
                  aria-label="Close"
                  onClick={closeModal}
                >
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>

              <div className="modal-body">
                <div className="container-fluid">
                  {errors.length > 0 ? (
                    <div
                      className="messages  alert alert-danger"
                      style={{
                        padding: "20px"
                      }}
                    >
                      {errors.map((error, key) => (
                        <div key={key}>{error.msg}</div>
                      ))}
                    </div>
                  ) : null}

                  {successmsg ? (
                    <div
                      className="messages alert alert-success"
                      style={{
                        padding: "20px"
                      }}
                    >
                      <div>{successmsg}</div>
                    </div>
                  ) : null}
                  <div className="form-group row">
                    <div
                      className="col-md-12 invoiceTypeContainer"
                      style={{ display: "flex", flexDirection: "column" }}
                    >
                      {allBanksArray ? (
                        <>
                          <div className="form-group row">
                            <div className="col-sm-12">
                              <select
                                className="form-control"
                                name="bankcode"
                                value={formdata.bankcode}
                                onChange={handleChange}
                              >
                                <option>Select Bank</option>
                                {allBanksArray.map((singleBank, key) => (
                                  <option key={key} value={singleBank.Code}>
                                    {singleBank.Name}
                                  </option>
                                ))}
                              </select>
                            </div>
                          </div>
                          <div className="form-group row">
                            <div className="col-sm-12">
                              <input
                                className="form-control"
                                type="tel"
                                placeholder="Account number"
                                name="accountnumber"
                                value={formdata.accountnumber}
                                onChange={handleChange}
                                maxLength={10}
                              />
                            </div>
                          </div>
                          <div className="form-group row">
                            <div className="col-sm-12">
                              <input
                                className="form-control"
                                type="text"
                                placeholder="Account Name"
                                name="accountname"
                                value={formdata.accountname}
                                readOnly
                              />
                            </div>
                          </div>
                          <div className="form-group row">
                            <div className="col-sm-12 add-account-switch">
                              <>
                                <div className="switchcontainer">
                                  <label className="switch">
                                    <input
                                      onChange={handleCheckboxChange}
                                      id="checkboxinput"
                                      type="checkbox"
                                    />{" "}
                                    <div></div>
                                  </label>
                                  <span>Set as primary account</span>
                                </div>
                              </>
                            </div>
                          </div>
                        </>
                      ) : (
                        <>{!errors ? <Loading /> : ""}</>
                      )}
                    </div>
                  </div>
                </div>
              </div>
              <div className="modal-footer">
                <button
                  onClick={handleSubmit}
                  className="btn btn-theme btn-sm "
                  disabled={isloading}
                >
                  <span name="save">{!isloading ? "Save" : "Saving..."}</span>
                  <div
                    className="lds-ring"
                    style={{
                      display: isloading ? "inline-block" : "none"
                    }}
                  >
                    <div></div>
                    <div></div>
                    <div></div>
                    <div></div>
                  </div>
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
      <div className="modal-backdrop fade show"></div>
    </>
  );
}
