import React from "react";
import banner from "../../assets/images/banner/01.png";
import bulb from "../../assets/images/banner/bulb.png";

const Hero = () => (
  <section id="home" className="banner p-0 pos-r fullscreen-banner">
    <div id="particles-js"></div>
    <div className="align-center">
      <div className="container">
        <div className="row align-items-center">
          <div className="col-lg-6 col-md-12">
            <img
              className="img-center wow fadeInUp"
              data-wow-duration="2.5s"
              src={banner}
              alt=""
            />
            <div className="img-2">
              <img className="img-center topBottom" src={bulb} alt="" />
            </div>
          </div>
          <div
            className="col-lg-6 col-md-12 md-mt-5 wow fadeInRight"
            data-wow-duration="2.5s"
          >
            <h5 className="title-theme-bg">Welcome To Sassaht</h5>
            <h1 className="mb-4">
              Build your Website With <span className="font-w-7">Sassaht</span>{" "}
              Code.
            </h1>
            <p className="lead mb-4">
              Sassaht Even the all-powerful Pointing has no control about the
              blind texts it is an almost unorthographic.
            </p>{" "}
            <a className="btn btn-theme" href="#">
              <span>Learn More</span>
            </a>
            <a className="btn btn-white" href="#">
              <span>Contact Us</span>
            </a>
          </div>
        </div>
      </div>
    </div>
  </section>
);

export default Hero;
