import React, { useState, useEffect } from "react";
import Loading from "../Loading";
import { connect } from "react-redux";
import { Link, useHistory } from "react-router-dom";
import Cookies from "js-cookie";
import axios from "axios";
import AddBankAccountModal from "./AddBankAccountModal";
import SingleBankAccountModal from "./SingleBankAccountModal";

//Actions
import { saveBankdata } from "../../actions/userActions";

const BankAccounts = ({ user, runSaveBankData }) => {
  let history = useHistory();
  const [errors, setErrors] = useState([]);
  const [allBanksArray, setAllBanksArray] = useState(null);
  const [allBankDetails, setAllBankDetails] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const [openedSingleAccount, setOpenedSingleAccount] = useState(null);

  const getBankdetails = async token => {
    try {
      const hasBankdetails = await axios.get("/account/get/allbankdetails", {
        headers: { "X-AUTH-TOKEN": token }
      });

      if (!hasBankdetails.data.status) {
        history.push("/dashboard/setup-biodata");
      } else {
        setAllBankDetails(hasBankdetails.data.bankdetails);
      }
    } catch (err) {
      //retry
      getBankdetails(token);
    }
  };

  const getAllBanks = async () => {
    try {
      const allbanks = await axios.get("/account/allbanks");
      if (allbanks.data.errors) {
        setErrors(allbanks.data.errors);
      } else {
        setAllBanksArray(allbanks.data);
      }
    } catch (error) {
      getAllBanks();
    }
  };
  const getBankNameByCode = (allBanksArray, bankcode) => {
    let currentbankname;
    allBanksArray.map(singleBank => {
      if (singleBank.Code === bankcode) {
        currentbankname = singleBank.Name;
      }
    });
    return currentbankname;
  };

  const showModalFtn = e => {
    e.preventDefault();
    setShowModal(true);
  };
  const closeModalFtn = e => {
    e.preventDefault();
    setShowModal(false);
    setOpenedSingleAccount(null);
  };

  const openAccountDetails = singleAccount => {
    setOpenedSingleAccount(singleAccount);
  };

  useEffect(() => {
    if (Cookies.get("TOKEN") !== null) {
      let token = Cookies.get("TOKEN");

      if (typeof token !== "undefined" && token !== null) {
        getBankdetails(token);
        getAllBanks();
      }
    }
  }, []);
  return (
    <>
      <div className="row">
        <div className="col-md-12 col-lg-12 col-xl-12">
          <div className="card m-b-30">
            <div className="card-body">
              <div
                className="invoice-top"
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "flex-start"
                }}
              >
                <h5 className="header-title pb-3 mt-0">Settlement Accounts</h5>
                <button className="btn btn-sm btn-theme" onClick={showModalFtn}>
                  <i className="mdi mdi-plus"></i>
                  Add Account
                </button>
              </div>
              <div id="multi-line-chart" style={{ minHeight: 400 }}>
                {showModal ? (
                  <AddBankAccountModal
                    closeModal={closeModalFtn}
                    allBanksArray={allBanksArray}
                  />
                ) : (
                  ""
                )}
                {openedSingleAccount ? (
                  <SingleBankAccountModal
                    account={openedSingleAccount}
                    allBanksArray={allBanksArray}
                    closeModal={closeModalFtn}
                  />
                ) : (
                  ""
                )}

                {allBankDetails ? (
                  <div className="table-responsive">
                    <table className="table table-hover custom-table">
                      <thead className="thead-light">
                        <tr style={{ fontWeight: 600 }}>
                          <th>Account name</th>
                          <th>Account number</th>
                          <th>Bank name</th>
                          <th>Status</th>
                          <th>Created</th>
                        </tr>
                      </thead>

                      <tbody>
                        {allBankDetails.map((singleBank, key) => (
                          <tr
                            key={key}
                            onClick={() => openAccountDetails(singleBank)}
                            style={{ cursor: "pointer" }}
                          >
                            <td>{singleBank.accountname}</td>
                            <td>{singleBank.accountnumber}</td>
                            <td>
                              {allBanksArray
                                ? getBankNameByCode(
                                    allBanksArray,
                                    singleBank.bankcode
                                  )
                                : ""}
                            </td>
                            <td>
                              <span
                                style={{
                                  padding: ".3rem 0.6rem",
                                  borderRadius: "1rem"
                                }}
                                className={
                                  singleBank.isprimary
                                    ? "badge badge-success"
                                    : "badge badge-default"
                                }
                              >
                                {singleBank.isprimary ? "primary" : "secondary"}
                              </span>
                            </td>
                            <td>
                              {new Date(singleBank.created).toDateString()}
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                ) : (
                  <Loading />
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

const mapStateToProps = state => ({
  user: state.user
});
const mapDispatchToProps = dispatch => ({
  runSaveBankData: data => {
    dispatch(saveBankdata(data));
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(BankAccounts);
