import React from "react";

const SummaryTile = () => {
  return (
    <div>
      <div className="row">
        <div className="col-md-6 col-lg-6 col-xl-3">
          <div className="card m-b-30" style={styles.card}>
            <div className="card-body">
              <div className="d-flex flex-row">
                <div className="col-3 align-self-center">
                  <div className="round">
                    <i className="mdi mdi-webcam"></i>
                  </div>
                </div>
                <div className="col-6 align-self-center text-center">
                  <div className="m-l-10">
                    <h5 className="mt-0 round-inner">$18090</h5>
                    <p className="mb-0 text-muted">Visits Today</p>
                  </div>
                </div>
                <div className="col-3 align-self-end align-self-center">
                  <h6 className="m-0 float-right text-center text-danger">
                    <i className="mdi mdi-arrow-down"></i> <span>5.26%</span>
                  </h6>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="col-md-6 col-lg-6 col-xl-3">
          <div className="card m-b-30" style={styles.card}>
            <div className="card-body">
              <div className="d-flex flex-row">
                <div className="col-3 align-self-center">
                  <div className="round">
                    <i className="mdi mdi-account-multiple-plus"></i>
                  </div>
                </div>
                <div className="col-6 text-center align-self-center">
                  <div className="m-l-10">
                    <h5 className="mt-0 round-inner">562</h5>
                    <p className="mb-0 text-muted">New Users</p>
                  </div>
                </div>
                <div className="col-3 align-self-end align-self-center">
                  <h6 className="m-0 float-right text-center text-success">
                    <i className="mdi mdi-arrow-up"></i> <span>8.68%</span>
                  </h6>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="col-md-6 col-lg-6 col-xl-3">
          <div className="card m-b-30" style={styles.card}>
            <div className="card-body">
              <div className="d-flex flex-row">
                <div className="col-3 align-self-center">
                  <div className="round">
                    <i className="mdi mdi-basket"></i>
                  </div>
                </div>
                <div className="col-6 align-self-center text-center">
                  <div className="m-l-10">
                    <h5 className="mt-0 round-inner">7514</h5>
                    <p className="mb-0 text-muted">New Orders</p>
                  </div>
                </div>
                <div className="col-3 align-self-end align-self-center">
                  <h6 className="m-0 float-right text-center text-danger">
                    <i className="mdi mdi-arrow-down"></i> <span>2.35%</span>
                  </h6>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="col-md-6 col-lg-6 col-xl-3">
          <div className="card m-b-30" style={styles.card}>
            <div className="card-body">
              <div className="d-flex flex-row">
                <div className="col-3 align-self-center">
                  <div className="round">
                    <i className="mdi mdi-rocket"></i>
                  </div>
                </div>
                <div className="col-6 align-self-center text-center">
                  <div className="m-l-10">
                    <h5 className="mt-0 round-inner">$32874</h5>
                    <p className="mb-0 text-muted">Total Sales</p>
                  </div>
                </div>
                <div className="col-3 align-self-end align-self-center">
                  <h6 className="m-0 float-right text-center text-success">
                    <i className="mdi mdi-arrow-up"></i> <span>2.35%</span>
                  </h6>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const styles = {
  card: {
    border: "none",
    minHeight: "10em"
  }
};
export default SummaryTile;
