import React, { useState, useEffect } from "react";
import axios from "axios";
import Cookies from "js-cookie";
import { useHistory } from "react-router-dom";

const UnverifiedEmailBanner = ({ email }) => {
  let history = useHistory();
  const [loading, setLoading] = useState(false);
  const [linkOutput, setLinkOutput] = useState("Resend Link");

  useEffect(() => {
    if (loading) {
      setLinkOutput("Sending...");
    }
  }, [loading]);

  const sendVerificationEmail = async e => {
    e.preventDefault();
    setLoading(true);
    //get token
    let token = Cookies.get("TOKEN");
    //get User
    try {
      const sendEmail = await axios.get("/auth/send-activation", {
        headers: { "X-AUTH-TOKEN": token }
      });
      setLoading(false);
      if (sendEmail.data.Error) {
        setLinkOutput("Try Again");
        console.log(sendEmail.data.Error);
      } else if (!sendEmail.data.emailSent) {
        history.push("/logout");
      } else {
        console.log(sendEmail.data);
        if (sendEmail.data.emailSent.accepted) {
          setLinkOutput("Email Sent");
        }
      }
    } catch (err) {
      setLoading(false);
      //retry
      sendVerificationEmail();
    }
  };
  return (
    <div
      className="alert alert-danger"
      style={{ margin: "0 -25px", borderRadius: 0, fontSize: 14 }}
    >
      Your email is yet to be verified, Click on the activation link sent to
      <b> {email}</b>
      <a
        style={{
          background: "transparent",
          textDecoration: "underline",
          fontSize: "14px",
          color: "#027718",
          padding: "5px 10px",
          boxShadow: "none"
        }}
        href="#"
        onClick={sendVerificationEmail}
        className="btm-sm"
      >
        {linkOutput}
        <div
          className="lds-ring"
          style={{
            display: loading ? "inline-block" : "none",
            width: 16,
            height: 16
          }}
        >
          <div style={styles.ringDiv}></div>
          <div style={styles.ringDiv}></div>
          <div style={styles.ringDiv}></div>
          <div style={styles.ringDiv}></div>
        </div>
        {/* {loading ? "Sending..." : "Resend"} */}
      </a>
    </div>
  );
};

const styles = {
  ringDiv: {
    width: 16,
    height: 16,
    margin: "0 6px"
  }
};

export default UnverifiedEmailBanner;
