import React, { useState } from "react";
import axios from "axios";
import Cookies from "js-cookie";

const InvoiceModal = ({ closeModal, updateStore }) => {
  const [isloading, setIsloading] = useState(false);
  const [selectionMade, setSelectionMade] = useState(false);
  const [formdata, setFormdata] = useState({
    invoicetype: ""
  });
  const handleChange = e => {
    setFormdata({ ...formdata, [e.target.name]: e.target.value });
    setSelectionMade(true);
  };
  const handleSubmit = async e => {
    e.preventDefault();
    setIsloading(true);
    let token = Cookies.get("TOKEN");
    try {
      const resp = await axios.post("/account/create/invoice", formdata, {
        headers: { "X-AUTH-TOKEN": token }
      });
      //setIsloading(false);
      //update redux store with new invoice and redirect
      updateStore(resp.data.invoice.txref);
      //   history.push(`/dashboard/invoices/${resp.data.invoice.txref}`);
    } catch (err) {
      setIsloading(false);
    }
  };
  return (
    <>
      <div
        className="modal fade show bs-example-modal-center"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="mySmallModalLabel"
        aria-hidden="true"
        style={{ display: "block", paddingRight: 17 }}
      >
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">
                Create Invoice
              </h5>
              <button
                type="button"
                className="close"
                data-dismiss="modal"
                aria-label="Close"
                onClick={closeModal}
              >
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">
              <div className="container-fluid">
                <form className="form">
                  <div className="form-group row">
                    <div
                      className="col-md-12 invoiceTypeContainer"
                      style={{ display: "flex" }}
                    >
                      <div className="form-check-inline my-1">
                        <div className="custom-control custom-radio">
                          <input
                            type="radio"
                            id="customRadio4"
                            name="invoicetype"
                            className="custom-control-input"
                            onChange={handleChange}
                            value="simple"
                          />
                          <label
                            className="custom-control-label"
                            htmlFor="customRadio4"
                          >
                            <span style={{ fontSize: 17, fontWeight: 600 }}>
                              Simple Invoice
                            </span>
                            <p>
                              Set amount, description, and request payment from
                              a customer.
                            </p>
                          </label>
                        </div>
                      </div>

                      <div className="form-check-inline my-1">
                        <div className="custom-control custom-radio">
                          <input
                            type="radio"
                            id="customRadio6"
                            name="invoicetype"
                            className="custom-control-input"
                            onChange={handleChange}
                            value="professional"
                          />
                          <label
                            className="custom-control-label"
                            htmlFor="customRadio6"
                          >
                            <span style={{ fontSize: 17, fontWeight: 600 }}>
                              Professional Invoice
                            </span>
                            <p>
                              Set line items, tax etc. and invoice a customer.
                              PDF included.
                            </p>
                          </label>
                        </div>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
            <div className="modal-footer">
              <button
                onClick={handleSubmit}
                className="btn btn-theme btn-sm "
                disabled={isloading || !selectionMade}
              >
                <span name="save">Continue</span>
                <div
                  className="lds-ring"
                  style={{
                    display: isloading ? "inline-block" : "none"
                  }}
                >
                  <div></div>
                  <div></div>
                  <div></div>
                  <div></div>
                </div>
              </button>
            </div>
          </div>
        </div>
      </div>
      <div className="modal-backdrop fade show"></div>
    </>
  );
};

export default InvoiceModal;
