import React, { useState, useEffect } from "react";
import Layout from "./layouts/WebsiteLayout";
import axios from "axios";
import { Link, Redirect, useHistory, useLocation } from "react-router-dom";
import Cookies from "js-cookie";

const Login = () => {
  let history = useHistory();
  const [formdata, setFormdata] = useState({
    email: "",
    password: ""
  });
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState([]);

  useEffect(() => {
    if (Cookies.get("TOKEN") !== null) {
      let token = Cookies.get("TOKEN");
      if (typeof token !== "undefined" && token !== null) {
        history.push("/dashboard");
      }
    }
  }, []);

  const handleChange = e => {
    setFormdata({ ...formdata, [e.target.name]: e.target.value });
  };

  const handleSubmit = async e => {
    e.preventDefault();
    setErrors([]);
    setLoading(true);
    //get data
    //validation

    try {
      //send data to API
      const response = await axios.post("/auth/login", formdata);
      setLoading(false);
      let { errors, token } = response.data;
      if (errors) {
        setErrors(errors);
        return;
      } else if (token !== "" && token !== null);
      {
        Cookies.set("TOKEN", token, { expires: 7 });
        history.push("/dashboard");
      }
    } catch (err) {
      setLoading(false);
      setErrors([{ msg: "An error occured with the server, Try again." }]);
    }
  };
  return (
    <Layout>
      <section className="login">
        <div className="container">
          <div className="row row-eq-height no-gutters sameheight">
            <div className="col-lg-4 col-md-12 ml-auto dark-bg px-4 align-item-middle text-center radius ">
              <div>
                <div className="section-title text-white">
                  <h2 className="title">Sign In</h2>
                  <div className="title-bdr">
                    <div className="left-bdr"></div>
                    <div className="right-bdr"></div>
                  </div>
                  <p>
                    Welcome To Sassaht, Please Enter Your Details And Start
                    Journy With Sassaht
                  </p>
                </div>
                <h5 className="mb-0 mt-4 text-capitalize text-white">
                  New to Payperless? <br /> <Link to="/register">Sign Up</Link>
                </h5>
              </div>
            </div>
            <div className="col-lg-6 col-md-12 mr-auto md-mt-5">
              <div className="login-form text-center white-bg box-shadow radius px-5 py-5 makeCenter">
                <div className="social-icons social-colored">
                  <ul className="list-inline">
                    <li className="">
                      <Link to="/">
                        <i className="mdi mdi-layers mdi-48px mdi-payperless-primary"></i>
                      </Link>
                    </li>
                  </ul>
                </div>
                <p className="my-3 font-italic"></p>
                <form id="contact-form" onSubmit={handleSubmit}>
                  <div
                    className="messages"
                    style={{
                      color: errors ? "red" : "#000",
                      padding: "20px 0px"
                    }}
                  >
                    {errors.map((error, key) => (
                      <div key={key}>{error.msg}</div>
                    ))}
                  </div>
                  <div className="form-group">
                    <input
                      id="form_email"
                      onChange={handleChange}
                      value={formdata.email}
                      autoComplete="false"
                      type="email"
                      name="email"
                      className="form-control"
                      placeholder="Email"
                      required="required"
                      data-error="Email is required."
                    />
                    <div className="help-block with-errors"></div>
                  </div>
                  <div className="form-group">
                    <input
                      id="form_password"
                      type="password"
                      name="password"
                      onChange={handleChange}
                      value={formdata.password}
                      className="form-control"
                      placeholder="Password"
                      required="required"
                      data-error="password is required."
                    />
                    <div className="help-block with-errors"></div>
                  </div>
                  <div className="form-group mt-4 mb-5">
                    <div className="remember-checkbox d-flex align-items-center justify-content-between">
                      <div className="checkbox">
                        <input type="checkbox" id="check2" name="check2" />
                        <label htmlFor="check2">Remember me</label>
                      </div>
                      <Link to="/forgot-password">Forgot Password?</Link>
                    </div>
                  </div>{" "}
                  <button
                    className="btn btn-theme text-uppercase"
                    disabled={loading}
                  >
                    <span>Sign In</span>
                    <div
                      className="lds-ring"
                      style={{ display: loading ? "inline-block" : "none" }}
                    >
                      <div></div>
                      <div></div>
                      <div></div>
                      <div></div>
                    </div>
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </section>
    </Layout>
  );
};

export default Login;
