import React from "react";

const Newsletter = () => (
  <section>
    <div className="container">
      <div className="row text-center">
        <div className="col-lg-8 col-md-12 ml-auto mr-auto">
          <div className="section-title">
            <h2 className="title">
              Subscribe Our <span>Newsletter</span>
            </h2>
            <div className="title-bdr">
              <div className="left-bdr"></div>
              <div className="right-bdr"></div>
            </div>
            <p>Get started for 1 Month free trial No Purchace required.</p>
          </div>
        </div>
      </div>
      <div className="row text-center">
        <div className="col-lg-8 col-md-10 ml-auto mr-auto">
          <div className="subscribe-form">
            <form id="mc-form" className="group d-md-flex align-items-center">
              <input
                type="email"
                value=""
                name="EMAIL"
                className="email"
                id="mc-email"
                placeholder="Enter Email Address"
                required=""
              />
              <input
                className="btn btn-theme"
                type="submit"
                name="subscribe"
                value="Subscribe"
              />
            </form>
          </div>
        </div>
      </div>
    </div>
  </section>
);

export default Newsletter;
