import React, { Fragment, useState, useEffect } from "react";
import axios from "axios";
import { useParams } from "react-router-dom";
import Layout from "./layouts/WebsiteLayout";
import Header from "./Home/Header";
import LoadingComponent from "./Loading";

const PayWithLink = () => {
  let { receivername } = useParams();
  const [fetchingReceiver, setFetchingReceiver] = useState(false);
  const [isReceiverValid, setisReceiverValid] = useState(null);
  const [errors, setErrors] = useState([]);
  const [receiver, setReceiver] = useState(null);
  const [acronym, setAcronym] = useState(null);
  const [currentView, setCurrentView] = useState("amount");
  const [paying, setPaying] = useState(false);
  const [redirecting, setRedirecting] = useState(false);
  const [storePicsumImage, setStorePicsumImage] = useState(null);
  const [randomColor, setRandomColor] = useState(null);
  const [formdata, setFormdata] = useState({
    amount: null,
    firstname: "",
    lastname: "",
    email: "",
    phone: ""
  });

  const fetchReceiver = async () => {
    setFetchingReceiver(true);
    try {
      const getReceiver = await axios.get(`/pay/${receivername}`);
      setFetchingReceiver(false);
      if (getReceiver.data.receiver) {
        setisReceiverValid(true);
        //set receiver in redux store
        setReceiver(getReceiver.data.receiver);
        calcInitials(getReceiver.data.receiver.name);
      } else if (getReceiver.data.errors) {
        setisReceiverValid(false);
        setErrors(getReceiver.data.errors);
      }
    } catch (err) {
      setFetchingReceiver(false);
      setErrors([
        { msg: "An error occured with the server, Refresh the page." }
      ]);
    }
  };
  const calcInitials = name => {
    let getacronym = name
      .split(/\s/)
      .reduce((response, word) => (response += word.slice(0, 1)), "");
    setAcronym(getacronym);
  };

  const getRandomColor = () => {
    let colors = [
      "#0064ff",
      "#a257da",
      "#b109aa",
      "#ad0e65",
      "#ad0e0e",
      "#85ad0e",
      "#0ead91",
      "#0e2fad",
      "#0dab57"
    ];
    let randomColor = colors[Math.floor(Math.random() * colors.length)];
    return randomColor;
  };

  const handleChange = e => {
    e.preventDefault();
    let val = e.target.value;
    setFormdata({
      ...formdata,
      [e.target.name]: e.target.value
    });
    if (e.target.name === "amount") {
      let amountCont = document.getElementById("amount-h2");
      if (e.target.value.length < 3) {
        amountCont.style.fontSize = "50px";
        e.target.style.width = "100px";
      } else if (e.target.value.length >= 3 && e.target.value.length < 5) {
        amountCont.style.fontSize = "42px";
        e.target.style.width = "100px";
      } else if (e.target.value.length >= 5 && e.target.value.length < 9) {
        amountCont.style.fontSize = "32px";
        e.target.style.width = "150px";
      }
    }
  };
  const moneyFormat = (price, sign = "$") => {
    const pieces = parseFloat(price)
      .toFixed(2)
      .split("");
    let ii = pieces.length - 3;
    while ((ii -= 3) > 0) {
      pieces.splice(ii, 0, ",");
    }
    return sign + pieces.join("");
  };
  const showNext = e => {
    e.preventDefault();
    setCurrentView("data");
  };
  const changeAmount = e => {
    e.preventDefault();
    setCurrentView("amount");
  };

  const handleSubmit = async e => {
    e.preventDefault();
    setPaying(true);

    try {
      const payURL = await axios.post(`/pay/${receivername}`, formdata);
      // setPaying(false);
      setRedirecting(true);
      // window.open(payURL.data.data.link, "_blank").location(); // On a new Tab
      window.location = payURL.data.data.link; // In current Tab
    } catch (err) {
      setPaying(false);
      setErrors([{ msg: "An error occured with the server, Try again." }]);
    }
  };
  useEffect(() => {
    setStorePicsumImage(
      `https://picsum.photos/id/${Math.floor(Math.random() * 1000)}/450/200`
    );
    setRandomColor(getRandomColor());
    //fetch receiver
    fetchReceiver();
  }, [receivername]);
  return (
    <Layout>
      <Header />
      {/* pricing Table */}

      <section>
        <div className="container">
          {receiver !== null ? (
            <div
              className="row align-items-center no-gutters"
              style={styles.flexCenter}
            >
              <div className="md-mt-5 paymentlink-paydiv">
                <div className="price-table active">
                  <div
                    className="price-header"
                    style={{
                      display: "flex",
                      flexDirection: "column",
                      justifyContent: "center",
                      alignItems: "center"
                    }}
                  >
                    <span
                      className="img-cont"
                      style={{
                        minWidth: 400,
                        minHeight: 100,
                        margin: "-80px -30px 0"
                      }}
                    >
                      <img src={storePicsumImage} alt="" />
                    </span>
                    <div
                      className="receiver-initials"
                      style={{
                        width: 100,
                        height: 100,
                        borderRadius: "50%",
                        background: randomColor,
                        color: "#fff",
                        marginTop: -50,
                        fontSize: 38,
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "center"
                      }}
                    >
                      {acronym ? acronym.toUpperCase() : ""}
                    </div>
                    <h3 className="price-title" style={{ margin: "20px 0 0" }}>
                      {receiver.name}
                    </h3>
                    {/* <small></small> */}
                    <div className="price-list">
                      <ul className="list-unstyled">
                        <li>
                          <small>{receiver.paymentlink}</small>
                        </li>
                      </ul>
                    </div>
                    {currentView === "amount" ? (
                      <Fragment>
                        <div className="price-value">
                          <h2 id="amount-h2">
                            <span>&#8358;</span>
                            <input
                              type="text"
                              value={
                                formdata.amount
                                  ? formdata.amount.toLocaleString()
                                  : ""
                              }
                              onChange={handleChange}
                              id="amount"
                              name="amount"
                              className="numberNoScroll"
                              style={styles.numberInput}
                              dir="ltr"
                              autoComplete="off"
                              placeholder="0.00"
                            />
                          </h2>
                          <span>NGN</span>
                        </div>

                        <a
                          className="btn btn-theme btn-circle mt-4"
                          href="#"
                          onClick={showNext}
                        >
                          <span>Next</span>
                        </a>
                      </Fragment>
                    ) : (
                      <Fragment>
                        <form
                          id="contact-form"
                          onSubmit={handleSubmit}
                          method="post"
                        >
                          <div className="messages"></div>
                          <div className="row">
                            <div className="col-md-6">
                              <div className="form-group">
                                <input
                                  id="form_name"
                                  type="text"
                                  name="firstname"
                                  className="form-control"
                                  placeholder="First name"
                                  required
                                  value={formdata.firstname}
                                  onChange={handleChange}
                                />
                                <div className="help-block with-errors"></div>
                              </div>
                            </div>
                            <div className="col-md-6">
                              <div className="form-group">
                                <input
                                  id="form_lastname"
                                  type="text"
                                  name="lastname"
                                  className="form-control"
                                  placeholder="Last name"
                                  required
                                  value={formdata.lastname}
                                  onChange={handleChange}
                                />
                                <div className="help-block with-errors"></div>
                              </div>
                            </div>
                          </div>
                          <div className="row">
                            <div className="col-md-6">
                              <div className="form-group">
                                <input
                                  id="form_email"
                                  type="email"
                                  name="email"
                                  className="form-control"
                                  placeholder="Email"
                                  value={formdata.email}
                                  onChange={handleChange}
                                />
                                <div className="help-block with-errors"></div>
                              </div>
                            </div>
                            <div className="col-md-6">
                              <div className="form-group">
                                <input
                                  id="form_phone"
                                  type="tel"
                                  name="phone"
                                  className="form-control"
                                  placeholder="Phone"
                                  value={formdata.phone}
                                  onChange={handleChange}
                                />
                                <div className="help-block with-errors"></div>
                              </div>
                            </div>
                          </div>
                          <div className="row">
                            <div className="col-md-12 mt-4 mb-4">
                              <button
                                className="btn btn-theme btn-circle"
                                disabled={paying}
                              >
                                <span>
                                  {paying ? (
                                    <Fragment>
                                      {redirecting ? "Redirecting" : "Paying"}{" "}
                                      <div className="lds-ring">
                                        <div></div>
                                        <div></div>
                                        <div></div>
                                        <div></div>
                                      </div>
                                    </Fragment>
                                  ) : (
                                    <Fragment>
                                      Pay{" "}
                                      {Number(formdata.amount).toLocaleString(
                                        "en-US",
                                        {
                                          style: "currency",
                                          currency: "NGN"
                                        }
                                      )}
                                    </Fragment>
                                  )}
                                </span>
                              </button>
                            </div>
                            <div className="col-md-12 mt-2">
                              <a href="#" onClick={changeAmount}>
                                Change amount
                              </a>
                            </div>
                          </div>
                        </form>
                      </Fragment>
                    )}
                  </div>
                </div>
              </div>
            </div>
          ) : (
            <div style={styles.flexCenter} className="md-mt-5">
              <LoadingComponent />
            </div>
          )}
        </div>
      </section>

      {/* pricing Table */}
    </Layout>
  );
};
const styles = {
  flexCenter: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    align: "center"
  },
  numberInput: {
    width: 100,
    border: "none"
  }
};

export default PayWithLink;
