import React, { useState, useEffect } from "react";
import Layout from "../layouts/Dashboard";
import axios from "axios";
import Cookies from "js-cookie";
import { Link, useHistory } from "react-router-dom";
import { state_and_lga } from "../../assets/states_and_lga";

const Biodata = () => {
  const [lgas, setLgas] = useState(["City"]);
  let history = useHistory();
  const [formData, setFormData] = useState({
    firstname: "",
    lastname: "",
    phone: "",
    state: "",
    city: "",
    address: ""
  });
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState(false);
  const handleChange = e => {
    e.preventDefault();
    setFormData({ ...formData, [e.target.id]: e.target.value });
    if (e.target.id === "state") {
      state_and_lga.map(state => {
        if (state.state === e.target.value) {
          setLgas(state.lgas);
        }
      });
    }
  };

  const handleSubmit = async e => {
    e.preventDefault();
    setErrors([]);
    setLoading(true);
    //get data
    //validation
    try {
      let token = Cookies.get("TOKEN");
      //send data to API
      const response = await axios.post("/account/create/biodata", formData, {
        headers: { "X-AUTH-TOKEN": token }
      });
      setLoading(false);
      let { errors, data } = response.data;
      if (errors) {
        setErrors(errors);
        return;
      } else if (data !== "" && data !== null);
      {
        history.push("/dashboard/setup-bank-details");
      }
    } catch (err) {
      setLoading(false);
      setErrors([{ msg: "An error occured with the server, Try again." }]);
    }
  };

  const getProfile = async token => {
    try {
      const hasProfile = await axios.get("/account/get/biodata", {
        headers: { "X-AUTH-TOKEN": token }
      });

      if (!hasProfile.data.status) {
        return;
      } else {
        history.push("/dashboard");
      }
    } catch (err) {
      //retry
      getProfile(token);
    }
  };

  useEffect(() => {
    if (Cookies.get("TOKEN") !== null) {
      let token = Cookies.get("TOKEN");

      if (typeof token !== "undefined" && token !== null) {
        //has User created a profile?
        getProfile(token);
      }
    }
  }, []);

  return (
    <Layout>
      <div className="container-fluid">
        <div className="row">
          <div className="col-sm-12">
            <div className="page-title-box">
              <div className="btn-group float-right">
                <ol className="breadcrumb hide-phone p-0 m-0">
                  <li className="breadcrumb-item">
                    <Link to="/dashboard">Dashboard</Link>
                  </li>
                  <li className="breadcrumb-item active">Bio-Data</li>
                </ol>
              </div>
              <h4 className="page-title">Complete Your Profile</h4>
            </div>
          </div>
        </div>

        <div className="row">
          <div className="col-12">
            <div className="card m-b-30">
              <div className="card-body">
                <h4 className="mt-0 header-title">Personal Details</h4>

                <form className="" onSubmit={handleSubmit}>
                  {errors ? (
                    <div
                      className="messages"
                      style={{
                        color: "red",
                        padding: "20px 0px"
                      }}
                    >
                      {errors.map((error, key) => (
                        <div key={key}>{error.msg}</div>
                      ))}
                    </div>
                  ) : null}
                  <div className="form-group row">
                    <div className="col-sm-6">
                      <input
                        className="form-control"
                        type="text"
                        placeholder="First name"
                        id="firstname"
                        value={formData.firstname}
                        onChange={handleChange}
                        required
                      />
                    </div>

                    <div className="col-sm-6">
                      <input
                        className="form-control"
                        type="text"
                        placeholder="Last name"
                        id="lastname"
                        value={formData.lastname}
                        onChange={handleChange}
                        required
                      />
                    </div>
                  </div>
                  <div className="form-group row">
                    <div className="col-sm-12">
                      <input
                        className="form-control"
                        type="tel"
                        placeholder="Phone number"
                        id="phone"
                        value={formData.phone}
                        onChange={handleChange}
                        required
                      />
                    </div>
                  </div>
                  <div className="form-group row">
                    <div className="col-sm-6">
                      <select
                        className="form-control"
                        id="state"
                        value={formData.state}
                        onChange={handleChange}
                        required
                      >
                        <option readOnly>
                          Where is your business located?
                        </option>
                        {state_and_lga.map((state, key) => (
                          <option value={state.state} key={key}>
                            {state.state}
                          </option>
                        ))}
                      </select>
                    </div>
                    <div className="col-sm-6">
                      <select
                        className="form-control"
                        id="city"
                        value={formData.city}
                        onChange={handleChange}
                        required
                      >
                        {lgas.map((lga, key) => (
                          <option value={lga} key={key}>
                            {lga}
                          </option>
                        ))}
                      </select>
                    </div>
                  </div>
                  <div className="form-group row">
                    <div className="col-sm-12">
                      <input
                        className="form-control"
                        type="text"
                        placeholder="Address"
                        id="address"
                        value={formData.address}
                        onChange={handleChange}
                        required
                      />
                    </div>
                  </div>

                  <button
                    type="submit"
                    className="btn float-right btn-theme text-uppercase ml-2"
                    disabled={loading}
                  >
                    Save
                    <div
                      className="lds-ring"
                      style={{ display: loading ? "inline-block" : "none" }}
                    >
                      <div></div>
                      <div></div>
                      <div></div>
                      <div></div>
                    </div>
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Biodata;
