import React, { useEffect } from "react";
import Layout from "../layouts/WebsiteLayout";
import bgImage from "../../assets/images/bg/09.png";
import logo from "../../assets/images/logo.png";
import comingSoon from "../../assets/images/superhero.svg";
import { connect } from "react-redux";
import axios from "axios";
import { Link, useHistory } from "react-router-dom";
import Cookies from "js-cookie";

//Actions
import { saveUserdata } from "../../actions/userActions";

const SetupComplete = ({ user, runSaveUserData }) => {
  let history = useHistory();

  const copytext = e => {
    /* Get the text field */
    let copyText = document.getElementById("paymentLink");

    /* Select the text field */
    copyText.select();
    copyText.setSelectionRange(0, 99999); /*For mobile devices*/

    /* Copy the text inside the text field */
    document.execCommand("copy");

    console.log("Copied the text: " + copyText.value);
    let thisBtn = e.target;
    thisBtn.style.backgroundColor = "#03d665";
    thisBtn.style.color = "#fff";
    thisBtn.innerHTML = "Copied!";
  };

  const validateToken = async token => {
    const isTokenValid = await axios.get("/auth/validate-token", {
      headers: { "X-AUTH-TOKEN": token }
    });
    if (!isTokenValid.data.status) {
      history.push("/logout");
    }
  };

  const getUserDetails = async token => {
    try {
      const userDetails = await axios.get("/account/get/user", {
        headers: { "X-AUTH-TOKEN": token }
      });

      if (!userDetails.data.status) {
        history.push("/logout");
      } else {
        runSaveUserData(userDetails.data);
      }
    } catch (err) {
      //retry
      getUserDetails(token);
    }
  };

  useEffect(() => {
    if (user.userdata === null) {
      if (Cookies.get("TOKEN") !== null) {
        let token = Cookies.get("TOKEN");

        if (typeof token !== "undefined" && token !== null) {
          //validate token
          validateToken(token);
          getUserDetails(token);
        }
      }
    }
  });

  return (
    <Layout>
      <section className="fullscreen-banner p-0 o-hidden">
        <div className="coming-soon-pattern">
          <img className="img-fluid" src={bgImage} alt="" />
        </div>
        <div className="align-center">
          <div className="container">
            <div className="row">
              <div className="col-lg-6 col-md-12">
                <div className="coming-soon">
                  <Link to="/dashboard">
                    <img
                      id="logo-img"
                      className="img-center"
                      src={logo}
                      alt=""
                      style={{ width: "30%" }}
                    />
                  </Link>
                  <h1 className="mt-4 mb-2 font-w-7">Congratulations!</h1>
                  <p>
                    You've successfully completed your account setup and can
                    immediately begin receiving payments from anyone globally!
                  </p>

                  {user.userdata !== null ? (
                    <div className="subscribe-form" style={styles.flexDiv}>
                      <h4 className="title z-index-1" style={styles.linkDiv}>
                        <textarea
                          id="paymentLink"
                          rows="1"
                          readOnly
                          style={styles.textarea}
                          value={user.userdata.userdetails.paymentlink}
                        ></textarea>
                      </h4>
                      <span
                        className="btn btn-sm btn-theme"
                        onClick={copytext}
                        style={styles.linkSpan}
                      >
                        Copy link
                      </span>
                    </div>
                  ) : (
                    <div>
                      <h4 className="title z-index-1 mb-4">
                        Generating link
                        <div className="lds-ring">
                          <div
                            style={{
                              borderColor:
                                "#03d665 transparent transparent transparent"
                            }}
                          ></div>
                          <div
                            style={{
                              borderColor:
                                "#03d665 transparent transparent transparent"
                            }}
                          ></div>
                          <div
                            style={{
                              borderColor:
                                "#03d665 transparent transparent transparent"
                            }}
                          ></div>
                          <div
                            style={{
                              borderColor:
                                "#03d665 transparent transparent transparent"
                            }}
                          ></div>
                        </div>
                      </h4>
                    </div>
                  )}

                  {user.userdata !== null ? (
                    <div>
                      <p>
                        Share this link with prospective clients on your social
                        media pages to seamlessly receive payments from a global
                        audience right away!
                      </p>
                      <div
                        className="share-div"
                        style={{ padding: "0 0 50px" }}
                      >
                        <span>
                          <b>Share on: </b>
                        </span>
                        <a
                          style={styles.sharelink}
                          href={`https://www.facebook.com/sharer/sharer.php?u=${user.userdata.userdetails.paymentlink}`}
                          target="_blank"
                        >
                          <i
                            className="mdi mdi-28px mdi-facebook"
                            style={{ color: "#2851A3" }}
                          ></i>
                        </a>
                        <a
                          style={styles.sharelink}
                          href={`https://twitter.com/intent/tweet?text=You%20can%20easily%20pay%20me%20via%20payperless%20by%20clicking%20on%20this%20link&url=${user.userdata.userdetails.paymentlink}`}
                          target="_blank"
                        >
                          <i
                            className="mdi mdi-28px mdi-twitter"
                            style={{ color: "#1da1f2" }}
                          ></i>
                        </a>
                        <a
                          style={styles.sharelink}
                          href={`https://wa.me/?text=You%20can%20easily%20pay%20me%20via%20payperless%20by%20clicking%20on%20this%20link%20${user.userdata.userdetails.paymentlink}`}
                          target="_blank"
                        >
                          <i className="mdi mdi-28px mdi-whatsapp"></i>
                        </a>
                      </div>
                    </div>
                  ) : (
                    ""
                  )}

                  <Link to="/dashboard">
                    <span className="btn btn-theme">Go to Dashboard </span>
                  </Link>
                </div>
              </div>
            </div>
            <div className="col-lg-6 col-md-12 image-column right p-0 md-mt-5">
              <img className="img-center" src={comingSoon} alt="" />
            </div>
          </div>
        </div>
      </section>
    </Layout>
  );
};

const styles = {
  textarea: {
    border: "none",
    resize: "none",
    width: "100%",
    fontWeight: 600,
    fontSize: "1.6rem"
    // height: 40
  },
  flexDiv: {
    display: "flex",
    flexWrap: "wrap"
  },
  linkDiv: {
    flexGrow: 3
  },
  linkSpan: {
    flexGrow: 1,
    alignSelf: "flex-start",
    backgroundColor: "transparent",
    color: "#000"
  },
  sharelink: {
    marginLeft: 20
  }
};

const mapStateToProps = state => ({
  user: state.user
});

const mapDispatchToProps = dispatch => ({
  runSaveUserData: data => {
    dispatch(saveUserdata(data));
  }
});
export default connect(mapStateToProps, mapDispatchToProps)(SetupComplete);
