import React, { useState, useEffect } from "react";
import axios from "axios";
import Loading from "../Loading";
import { useHistory } from "react-router-dom";
import Cookies from "js-cookie";

export default function SingleBankAccount({
  closeModal,
  allBanksArray,
  account
}) {
  let history = useHistory();
  const [isloading, setIsloading] = useState(false);
  const [successmsg, setSuccessmsg] = useState(null);
  const [errors, setErrors] = useState([]);
  const [formdata, setFormdata] = useState({
      id: account._id,
      userid: account.userid,
      bankcode: account.bankcode,
      accountname : account.accountname,
      accountnumber : account.accountnumber,
      isprimary: account.isprimary,
  });
  const getBankNameByCode = (allBanksArray, bankcode) => {
    let currentbankname;
    allBanksArray.map(singleBank => {
      if (singleBank.Code === bankcode) {
        currentbankname = singleBank.Name;
      }
    });
    return currentbankname;
  };

  const handleCheckboxChange = e => {
    // e.preventDefault();
    let box = document.getElementById("checkboxinput");
    setFormdata({ ...formdata, isprimary: box.checked });
  };
  const handleSubmit = async e => {
    e.preventDefault();
    setErrors([]);
    setIsloading(true);
    //get data
    //validation

    try {
      let token = Cookies.get("TOKEN");
      //send data to API
      console.log(formdata);
      const response = await axios.post(`/account/edit/bank/${account._id}`, formdata, {
        headers: { "X-AUTH-TOKEN": token }
      });
      setIsloading(false);
      console.log(response.data);
      let { errors, data } = response.data;
      if (errors) {
        setErrors(errors);
        return;
      } else if (data !== "" && data !== null);
      {
        window.location.reload();
        document.getElementById("closemodal").click();
      }
    } catch (err) {
      setIsloading(false);
      setErrors([{ msg: "An error occured with the server, Try again." }]);
    }
  };

  return (
    <>
      <div
        className="modal fade show bs-example-modal-center"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="mySmallModalLabel"
        aria-hidden="true"
        style={{ display: "block", paddingRight: 17 }}
      >
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <form className="form">
              <div className="modal-header">
                <button
                  type="button"
                  className="close"
                  id="closemodal"
                  data-dismiss="modal"
                  aria-label="Close"
                  onClick={closeModal}
                >
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>

              <div className="modal-body">
                <div className="container-fluid">
                  {errors.length > 0 ? (
                    <div
                      className="messages  alert alert-danger"
                      style={{
                        padding: "20px"
                      }}
                    >
                      {errors.map((error, key) => (
                        <div key={key}>{error.msg}</div>
                      ))}
                    </div>
                  ) : null}

                  {successmsg ? (
                    <div
                      className="messages alert alert-success"
                      style={{
                        padding: "20px"
                      }}
                    >
                      <div>{successmsg}</div>
                    </div>
                  ) : null}
                  <div className="form-group row">
                    <div
                      className="col-md-12 invoiceTypeContainer"
                      style={{ display: "flex", flexDirection: "column" }}
                    >
                      {allBanksArray ? (
                        <>
                          <div className="form-group row">
                            <div className="col-sm-12">
                              <select
                                className="form-control"
                                name="bankcode"
                                value={account.bankcode}
                                readOnly
                              >
                                <option>
                                  {getBankNameByCode(
                                    allBanksArray,
                                    account.bankcode
                                  )}
                                </option>
                              </select>
                            </div>
                          </div>
                          <div className="form-group row">
                            <div className="col-sm-12">
                              <input
                                className="form-control"
                                type="tel"
                                placeholder="Account number"
                                name="accountnumber"
                                value={account.accountnumber}
                                maxLength={10}
                                readOnly
                              />
                            </div>
                          </div>
                          <div className="form-group row">
                            <div className="col-sm-12">
                              <input
                                className="form-control"
                                type="text"
                                placeholder="Account Name"
                                name="accountname"
                                value={account.accountname}
                                readOnly
                              />
                            </div>
                          </div>
                          {!account.isprimary ? (
                            <div className="form-group row">
                              <div className="col-sm-12 add-account-switch">
                                <>
                                  <div className="switchcontainer">
                                    <label className="switch">
                                      <input
                                        onChange={handleCheckboxChange}
                                        id="checkboxinput"
                                        type="checkbox"
                                      />{" "}
                                      <div></div>
                                    </label>
                                    <span>Set as primary account</span>
                                  </div>
                                </>
                              </div>
                            </div>
                          ) : null}
                        </>
                      ) : (
                        <>{!errors ? <Loading /> : ""}</>
                      )}
                    </div>
                  </div>
                </div>
              </div>
              {!account.isprimary ? (
                <div className="modal-footer">
                  <button
                    onClick={handleSubmit}
                    className="btn btn-theme btn-sm "
                    disabled={isloading}
                  >
                    <span name="save">
                      {!isloading ? "Update" : "Updating..."}
                    </span>
                    <div
                      className="lds-ring"
                      style={{
                        display: isloading ? "inline-block" : "none"
                      }}
                    >
                      <div></div>
                      <div></div>
                      <div></div>
                      <div></div>
                    </div>
                  </button>
                </div>
              ) : null}
            </form>
          </div>
        </div>
      </div>
      <div className="modal-backdrop fade show"></div>
    </>
  );
}
