import React, { useState, useEffect } from "react";
import Loading from "../Loading";
import { connect } from "react-redux";
import { useHistory } from "react-router-dom";
import Cookies from "js-cookie";
import axios from "axios";

//Actions
import { saveUserdata } from "../../actions/userActions";

const Preferences = ({ user, runSaveUserData }) => {
  let history = useHistory();
  const [filldata, setFilldata] = useState(false);
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState([]);
  const [successmsg, setSuccessmsg] = useState(null);
  const [formdata, setFormdata] = useState({
    settlementtype: "",
    userreceipt: "",
    customerreceipt: ""
  });

  const filldataFtn = () => {
    if (formdata.settlementtype === "next-day") {
      document.getElementById("customRadio6").checked = true;
    } else if (formdata.settlementtype === "instant") {
      document.getElementById("customRadio4").checked = true;
    }
    if (formdata.userreceipt)
      document.getElementById("customCheck6").checked = true;
    if (formdata.customerreceipt)
      document.getElementById("customCheck7").checked = true;
    setFilldata(false);
  };
  if (filldata) filldataFtn();

  const getUserDetails = async token => {
    try {
      const userDetails = await axios.get("/account/get/user", {
        headers: { "X-AUTH-TOKEN": token }
      });

      if (!userDetails.data.status) {
        history.push("/logout");
      } else {
        setFormdata({
          ...formdata,
          settlementtype: userDetails.data.userdetails.settlementtype,
          userreceipt: userDetails.data.userdetails.userreceipt,
          customerreceipt: userDetails.data.userdetails.customerreceipt
        });
        runSaveUserData(userDetails.data);
        setFilldata(true);
      }
    } catch (err) {
      //retry
      getUserDetails(token);
    }
  };

  const handleChange = e => {
    // e.preventDefault();
    if (
      e.target.name === "userreceipt" ||
      e.target.name === "customerreceipt"
    ) {
      setFormdata({ ...formdata, [e.target.name]: e.target.checked });
    } else {
      setFormdata({ ...formdata, [e.target.name]: e.target.value });
    }
  };
  const handleSubmit = async e => {
    e.preventDefault();
    setLoading(true);
    try {
      let token = Cookies.get("TOKEN");
      const resp = await axios.post("/account/edit/settings", formdata, {
        headers: { "X-AUTH-TOKEN": token }
      });
      console.log(resp.data);
      setLoading(false);
      let { errors, status } = resp.data;
      if (errors) {
        setErrors(errors);
        return;
      } else if (status);
      {
        setSuccessmsg(resp.data.msg);
      }
    } catch (err) {
      setLoading(false);
      setErrors([{ msg: "An error occured with the server, Try again." }]);
    }
  };

  useEffect(() => {
    if (Cookies.get("TOKEN") !== null) {
      let token = Cookies.get("TOKEN");

      if (typeof token !== "undefined" && token !== null) {
        //get and save user details
        if (user.userdata === null) {
          getUserDetails(token);
        } else {
          setFormdata({
            ...formdata,
            settlementtype: user.userdata.userdetails.settlementtype,
            userreceipt: user.userdata.userdetails.userreceipt,
            customerreceipt: user.userdata.userdetails.customerreceipt
          });
          setFilldata(true);
        }
      }
    }
  }, []);
  return (
    <>
      <div className="row">
        <div className="col-md-12 col-lg-12 col-xl-12">
          <div className="card m-b-30">
            <div className="card-body">
              <div
                className="invoice-top"
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "flex-start"
                }}
              >
                <h5 className="header-title pb-3 mt-0">Preferences</h5>
                <button
                  className="btn btn-sm btn-theme text-uppercase"
                  onClick={handleSubmit}
                  disabled={loading}
                >
                  <i className="mdi mdi-save"></i>
                  {!loading ? "Save" : "Saving..."}
                  <div
                    className="lds-ring"
                    style={{ display: loading ? "inline-block" : "none" }}
                  >
                    <div></div>
                    <div></div>
                    <div></div>
                    <div></div>
                  </div>
                </button>
              </div>
              <hr />
              <div id="multi-line-chart" style={{ minHeight: 400 }}>
                {user.userdata ? (
                  <div className="">
                    <div className="row">
                      <div className="col-sm-12">
                        {errors.length > 0 ? (
                          <div
                            className="messages  alert alert-danger"
                            style={{
                              padding: "20px"
                            }}
                          >
                            {errors.map((error, key) => (
                              <div key={key}>{error.msg}</div>
                            ))}
                          </div>
                        ) : null}

                        {successmsg ? (
                          <div
                            className="messages alert alert-success"
                            style={{
                              padding: "20px"
                            }}
                          >
                            <div>{successmsg}</div>
                          </div>
                        ) : null}
                      </div>
                      <div className="clearfix"></div>
                      <div className="col-sm-4">
                        How do you want to receive settlements
                      </div>
                      <div className="col-sm-8">
                        <div className="form-group row">
                          <div
                            className="col-md-12 invoiceTypeContainer"
                            style={{
                              display: "flex"
                              // flexDirection: "column"
                            }}
                          >
                            <div className="form-check-inline my-1">
                              <div className="custom-control custom-radio">
                                <input
                                  type="radio"
                                  id="customRadio4"
                                  name="settlementtype"
                                  className="custom-control-input"
                                  onChange={handleChange}
                                  value="instant"
                                  // checked={
                                  //   formdata.settlementtype === "instant"
                                  //     ? true
                                  //     : false
                                  // }
                                />
                                <label
                                  className="custom-control-label"
                                  htmlFor="customRadio4"
                                >
                                  <span
                                    style={{
                                      fontSize: 16,
                                      fontWeight: 500
                                    }}
                                  >
                                    Instantly
                                  </span>
                                  <p>
                                    <small>
                                      Payments will be sent to you within 5mins
                                      after each successful transaction. You
                                      would be charged NGN55 for each transfer
                                      to your bank account
                                    </small>
                                  </p>
                                </label>
                              </div>
                            </div>
                            <div className="form-check-inline my-1">
                              <div className="custom-control custom-radio">
                                <input
                                  type="radio"
                                  id="customRadio6"
                                  name="settlementtype"
                                  className="custom-control-input"
                                  onChange={handleChange}
                                  value="next-day"
                                  // checked={
                                  //   formdata.settlementtype === "next-day"
                                  //     ? true
                                  //     : false
                                  // }
                                />
                                <label
                                  className="custom-control-label"
                                  htmlFor="customRadio6"
                                >
                                  <span
                                    style={{
                                      fontSize: 16,
                                      fontWeight: 500
                                    }}
                                  >
                                    Next Day
                                  </span>
                                  <p>
                                    <small>
                                      All Payments you receive in a day will be
                                      sent to you once a day at 07:00 AM (GMT+1)
                                      the next day. You would be charged NGN55
                                      only once each day a transfer is made to
                                      your bank account
                                    </small>
                                  </p>
                                </label>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="clearfix"></div>
                    <hr />
                    <div className="row">
                      <div className="col-sm-4">
                        Receive email notification for transactions
                      </div>
                      <div className="col-sm-8">
                        <div className="form-check-inline my-2">
                          <div className="custom-control custom-checkbox">
                            <input
                              type="checkbox"
                              className="custom-control-input"
                              id="customCheck6"
                              name="userreceipt"
                              onChange={handleChange}
                            />
                            <label
                              className="custom-control-label"
                              htmlFor="customCheck6"
                            ></label>
                          </div>
                        </div>
                      </div>
                    </div>

                    <div className="clearfix"></div>
                    <hr />
                    <div className="row">
                      <div className="col-sm-4">
                        Send email receipts to customers
                      </div>
                      <div className="col-sm-8">
                        <div className="form-check-inline my-2">
                          <div className="custom-control custom-checkbox">
                            <input
                              type="checkbox"
                              className="custom-control-input"
                              id="customCheck7"
                              name="customerreceipt"
                              onChange={handleChange}
                            />
                            <label
                              className="custom-control-label"
                              htmlFor="customCheck7"
                            ></label>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                ) : (
                  <Loading />
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

const mapStateToProps = state => ({
  user: state.user
});
const mapDispatchToProps = dispatch => ({
  runSaveUserData: data => {
    dispatch(saveUserdata(data));
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(Preferences);
