import React, { useEffect, useState } from "react";
import axios from "axios";
import { Link, useParams } from "react-router-dom";
import Layout from "./layouts/WebsiteLayout";
import Header from "./Home/Header";
import confirmedLogo from "../assets/images/payment_confirmed.svg";
import Loading from "./Loading";

const PayWithLink = () => {
  const { txref } = useParams();
  const [payment, setPayment] = useState(null);
  const [errors, setErrors] = useState([]);

  const prettyCurrency = amount => {
    const formatter = new Intl.NumberFormat("en-NG", {
      style: "currency",
      currency: "NGN",
      minimumFractionDigits: 2
    });
    const convertedAmount = formatter.format(amount);
    return convertedAmount;
  };

  const getTransaction = async txref => {
    try {
      const transaction = await axios.get(
        `/pay/payment-link-callback/${txref}`
      );
      if (transaction.data.status) {
        setPayment(transaction.data.transaction);
      } else if (transaction.data.errors) {
        setErrors(transaction.data.errors);
      }
    } catch (err) {
      getTransaction(txref);
    }
  };
  useEffect(() => {
    getTransaction(txref);
  }, []);
  console.log(payment);
  return (
    <Layout>
      <Header />
      {/* pricing Table */}

      <section>
        <div className="container">
          <div
            className="row align-items-center no-gutters"
            style={styles.flexCenter}
          >
            <div className="md-mt-5 paymentlink-paydiv">
              <div className="price-table active">
                <div
                  className="price-header"
                  style={{
                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "center",
                    alignItems: "center"
                  }}
                >
                  {
                    payment ? 
                    <>
                  <div className="price-value">
                    <img
                      src={confirmedLogo}
                      alt=""
                      style={{
                        width: 250,
                        height: 250,
                        borderRadius: "50%",
                        margin: "auto"
                      }}
                    />
                    <h4 style={{ color: "#03d665" }}>Payment Successful!</h4>
                    <h2 id="amount-h2">
                          <input
                            type="text"
                            value={
                              payment.amount
                                ? prettyCurrency(payment.amount)
                                : ""
                            }
                            id="amount"
                            name="amount"
                            className="numberNoScroll"
                            style={styles.numberInput}
                            dir="ltr"
                            autoComplete="off"
                            placeholder="0.00"
                            readOnly
                          />
                        </h2>
                        <p style={{ padding: "20px 0 0" }}>
                          <u>
                            <Link to={`/payments/receipt/${txref}`}>
                              Download Receipt
                            </Link>
                          </u>
                        </p>
                  </div>
                  <hr style={{ border: "1px solid #ccc", width: "100%" }} />
                  <p  style={{ marginTop: "1rem" }}>
                    Do you have prospective clients online? <br />
                    Easily and Seamlessly receive payments like a PRO.
                  </p>
                  {/* <p>
                      Do business like a professional and gain trust, <br />
                      Start receiving payments from your online clients easily.
                    </p> */}
                  <Link
                    className="btn btn-theme btn-circle"
                    to="/register"
                  >
                    <span>Get Started</span>
                  </Link>
                  </>
                  :
                  <Loading/>
                  }
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* pricing Table */}
    </Layout>
  );
};
const styles = {
  flexCenter: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    align: "center"
  },
  numberInput: {
    width: 280,
    border: "none",
    textAlign: "center"
  }
};
export default PayWithLink;
