import React, { useState, useEffect } from "react";
import Layout from "../layouts/Dashboard";
import { connect } from "react-redux";
import Breadcrumb from "./Breadcrumb";
import axios from "axios";
import Cookies from "js-cookie";
import Loading from "../Loading";
import { useHistory } from "react-router-dom";
import { saveInvoice } from "../../actions/userActions";
import InvoiceModal from "./InvoiceModal";

const Invoices = ({ user, runSaveInvoice }) => {
  let history = useHistory();
  const [invoice, setInvoice] = useState(null);
  const [invoiceCount, setInvoiceCount] = useState(0);
  const [errors, setErrors] = useState([]);
  const [showModal, setShowModal] = useState(false);

  const getInvoices = async token => {
    try {
      const getInvoice = await axios.get("/account/get/invoice/all", {
        headers: { "X-AUTH-TOKEN": token }
      });
      if (getInvoice.data.errors) {
        setErrors(getInvoice.data.errors);
      } else if (getInvoice.data.invoice) {
        runSaveInvoice(getInvoice.data.invoice);
        getCompletedInvoices(token);
      }
    } catch (err) {
      getInvoices(token);
    }
  };
  const updateStoreFtn = async txref => {
    let token = Cookies.get("TOKEN");
    const getInvs = await getInvoices(token);
    history.push(`/dashboard/invoices/${txref}`);
  };
  const getCompletedInvoices = async token => {
    try {
      const getCompletedInvoice = await axios.get(
        "/account/get/invoice/completed",
        {
          headers: { "X-AUTH-TOKEN": token }
        }
      );
      setInvoice(getCompletedInvoice.data.invoice);
      setInvoiceCount(getCompletedInvoice.data.invoice.length);
    } catch (err) {
      getCompletedInvoices(token);
    }
  };

  const prettyCurrency = amount => {
    const formatter = new Intl.NumberFormat("en-NG", {
      style: "currency",
      currency: "NGN",
      minimumFractionDigits: 2
    });
    const convertedAmount = formatter.format(amount);
    return convertedAmount;
  };

  const openInvoice = txref => {
    history.push(`/dashboard/invoices/${txref}`);
  };

  const showModalFtn = () => {
    setShowModal(true);
  };
  const closeModalFtn = () => {
    setShowModal(false);
  };
  useEffect(() => {
    if (Cookies.get("TOKEN") !== null) {
      let token = Cookies.get("TOKEN");
      if (typeof token !== "undefined" && token !== null) {
        //check for invoice in redux store
        if (user.invoice) {
          getCompletedInvoices(token);
        } else {
          //get all invoices
          getInvoices(token);
        }
      }
    }
  }, []);

  console.log(invoice);
  return (
    <Layout>
      <Breadcrumb primary="Invoices" secondary="All invoices" />

      <div className="row">
        <div className="col-md-12 col-lg-12 col-xl-12">
          <div className="card m-b-30">
            <div className="card-body">
              <div
                className="invoice-top"
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "flex-start"
                }}
              >
                <h5 className="header-title pb-3 mt-0">
                  {invoiceCount > 1
                    ? invoiceCount + " Invoices"
                    : invoiceCount + " Invoice"}
                </h5>
                <button className="btn btn-sm btn-theme" onClick={showModalFtn}>
                  <i className="mdi mdi-plus"></i>
                  New Invoice
                </button>
              </div>
              <div id="multi-line-chart" style={{ height: 400 }}>
                {showModal ? (
                  <InvoiceModal
                    closeModal={closeModalFtn}
                    updateStore={updateStoreFtn}
                  />
                ) : (
                  ""
                )}

                {invoice ? (
                  <div className="table-responsive">
                    <table className="table table-hover custom-table">
                      <thead className="thead-light">
                        <tr style={{ fontWeight: 600 }}>
                          <th>Status</th>
                          <th>Customer</th>
                          <th>Amount</th>
                          <th>Invoice no.</th>
                          <th>Date</th>
                          <th>Payment Status</th>
                        </tr>
                      </thead>

                      <tbody>
                        {invoice.map((inv, key) => (
                          <tr
                            key={key}
                            onClick={() => openInvoice(inv.txref)}
                            style={{ cursor: "pointer" }}
                          >
                            <td
                              style={{
                                color:
                                  inv.forwardstatus === "save-only"
                                    ? "#797777"
                                    : "#03d665"
                              }}
                            >
                              {inv.forwardstatus === "save-only"
                                ? "Draft"
                                : "Sent"}
                            </td>
                            <td>{inv.customer.email}</td>
                            <td>{prettyCurrency(inv.amount)}</td>
                            <td>#{inv.invoiceNumber}</td>
                            <td>
                              {new Date(inv.created).toDateString()}
                            </td>
                            <td>
                              <span
                                style={{
                                  padding: ".4rem 1.2rem",
                                  borderRadius: "1rem"
                                }}
                                className={
                                  inv.paymentstatus === "successful"
                                    ? "badge badge-success"
                                    : "badge badge-warning"
                                }
                              >
                                {inv.paymentstatus === "successful"
                                  ? "Paid"
                                  : "Pending"}
                              </span>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                ) : (
                  <Loading />
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

const mapStateToProps = state => ({
  user: state.user
});

const mapDispatchToProps = dispatch => ({
  runSaveInvoice: data => {
    dispatch(saveInvoice(data));
  }
});
export default connect(mapStateToProps, mapDispatchToProps)(Invoices);
