import React, { Fragment } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { createStore, compose, applyMiddleware } from "redux";
import { Provider } from "react-redux";
import thunk from "redux-thunk";

import Dashboard from "./components/Dashboard/";
import SetupBiodata from "./components/Dashboard/SetupBiodata";
import SetupBankDetails from "./components/Dashboard/SetupBankDetails";
import SetupComplete from "./components/Dashboard/SetupComplete";
import Invoices from "./components/Dashboard/Invoices";
import SingleInvoice from "./components/Dashboard/SingleInvoice";
import Transactions from "./components/Dashboard/Transactions";
import SingleTransaction from "./components/Dashboard/SingleTransaction";
import Settings from "./components/Dashboard/Settings";
import Profile from "./components/Dashboard/Profile";
import Preferences from "./components/Dashboard/Preferences";
import BankAccounts from "./components/Dashboard/BankAccounts";
import Business from "./components/Dashboard/BusinessSettings";

import rootReducer from "./reducers/";
import ResetpasswordComponent from "./components/Resetpassword";
import ForgotpasswordComponent from "./components/Forgotpassword";
import LoginComponent from "./components/Login";
import RegisterComponent from "./components/Register";
import HomeComponent from "./components/Home/";
import NotFound from "./components/NotFound";
import ProtectedRoute from "./components/middleware/ProtectedRoute";
import ActivateAccount from "./components/AccountActivated";
import LogoutComponent from "./components/Logout";
import PayWithLink from "./components/PayWithLink";
import PayWithInvoice from "./components/PayWithInvoice";
import PayWithLinkSuccess from "./components/PayWithLinkSuccess";
import PayWithInvoiceSuccess from "./components/PayWithInvoiceSuccess";
import Receipt from "./components/Receipt";

const middleware = [thunk];

const store = createStore(
  rootReducer,
  {},
  compose(
    applyMiddleware(...middleware),
    window.__REDUX_DEVTOOLS_EXTENSION__
      ? window.__REDUX_DEVTOOLS_EXTENSION__()
      : f => f
  )
);

const App = () => (
  <Provider store={store}>
    <Router>
      <Switch>
        <Route path="/" component={HomeComponent} exact />
        <Route path="/forgot-password" component={ForgotpasswordComponent} />
        <Route
          path="/reset-password/:code"
          component={ResetpasswordComponent}
        />
        <Route path="/activate-account/:code" component={ActivateAccount} />
        <Route path="/register" component={RegisterComponent} />
        <Route path="/logout" component={LogoutComponent} />
        <Route path="/login" component={LoginComponent} />
        <Route path="/payments/receipt/:txref" exact component={Receipt} />
        <Route
          path="/pay/payment-link-callback/:txref"
          exact
          component={PayWithLinkSuccess}
        />
        <Route
          path="/pay-invoice/payment-redirect/:txref"
          exact
          component={PayWithInvoiceSuccess}
        />
        <Route path="/pay/:receivername" component={PayWithLink} />
        <Route path="/pay-invoice/:invoicetxref" component={PayWithInvoice} />

        <ProtectedRoute path="/dashboard" exact>
          <Dashboard />
        </ProtectedRoute>
        <ProtectedRoute path="/dashboard/setup-biodata">
          <SetupBiodata />
        </ProtectedRoute>
        <ProtectedRoute path="/dashboard/setup-bank-details">
          <SetupBankDetails />
        </ProtectedRoute>
        <ProtectedRoute path="/dashboard/setup-complete">
          <SetupComplete />
        </ProtectedRoute>
        <ProtectedRoute path="/dashboard/invoices" exact>
          <Invoices />
        </ProtectedRoute>
        <ProtectedRoute path="/dashboard/invoices/:txref" exact>
          <SingleInvoice />
        </ProtectedRoute>
        <ProtectedRoute path="/dashboard/transactions" exact>
          <Transactions />
        </ProtectedRoute>
        <ProtectedRoute path="/dashboard/transactions/:txref" exact>
          <SingleTransaction />
        </ProtectedRoute>
        <ProtectedRoute path="/dashboard/settings/profile">
          <Settings>
            <Profile />
          </Settings>
        </ProtectedRoute>
        <ProtectedRoute path="/dashboard/settings/preferences">
          <Settings>
            <Preferences />
          </Settings>
        </ProtectedRoute>
        <ProtectedRoute path="/dashboard/settings/bank-accounts">
          <Settings>
            <BankAccounts />
          </Settings>
        </ProtectedRoute>
        <ProtectedRoute path="/dashboard/settings/business">
          <Settings>
            <Business />
          </Settings>
        </ProtectedRoute>
        <Route component={NotFound} />
      </Switch>
    </Router>
  </Provider>
);

export default App;
