//Vendor CSS
import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import Cookies from "js-cookie";
import axios from "axios";
import { Link, useHistory } from "react-router-dom";
import "pace-progressbar";
import "pace-progressbar/themes/green/pace-theme-flash.css";
import "../../dashboard_assets/plugins/morris/morris.css";
import "../../dashboard_assets/css/bootstrap.min.css";
import "../../dashboard_assets/css/icons.css";
import "../../dashboard_assets/css/style.css";

import LeftMenu from "../Dashboard/LeftMenu";
import TopBar from "../Dashboard/TopBar";
import Unverified_Email_Banner from "../Dashboard/UnverifiedEmailBanner";

//Actions
import {
  saveBiodata,
  saveBankdata,
  saveUserdata
} from "../../actions/userActions";

const Layout = ({
  children,
  user,
  runSaveBioData,
  runSaveBankData,
  runSaveUserData
}) => {
  let history = useHistory();

  const validateToken = async token => {
    const isTokenValid = await axios.get("/auth/validate-token", {
      headers: { "X-AUTH-TOKEN": token }
    });
    if (!isTokenValid.data.status) {
      history.push("/logout");
    }
  };

  const getProfile = async token => {
    try {
      const hasProfile = await axios.get("/account/get/biodata", {
        headers: { "X-AUTH-TOKEN": token }
      });

      if (!hasProfile.data.status) {
        history.push("/dashboard/setup-biodata");
      } else {
        //set biodata to state via redux
        runSaveBioData(hasProfile.data);
        const hasBank = await axios.get("/account/get/bankdetails", {
          headers: { "X-AUTH-TOKEN": token }
        });
        if (!hasBank.data.status) {
          history.push("/dashboard/setup-bank-details");
        } else {
          //set bank data to state via redux
          runSaveBankData(hasBank.data);
        }
      }
    } catch (err) {
      //retry
      getProfile(token);
    }
  };

  const getUserDetails = async token => {
    try {
      const userDetails = await axios.get("/account/get/user", {
        headers: { "X-AUTH-TOKEN": token }
      });

      if (!userDetails.data.status) {
        history.push("/logout");
      } else {
        runSaveUserData(userDetails.data);
      }
    } catch (err) {
      //retry
      getUserDetails(token);
    }
  };

  useEffect(() => {
    if (Cookies.get("TOKEN") !== null) {
      let token = Cookies.get("TOKEN");

      if (typeof token !== "undefined" && token !== null) {
        //validate token
        validateToken(token);
        //has User created a profile?
        if (user.biodata === null || user.bankdata === null) getProfile(token);
        //get and save user details
        if (user.userdata === null) getUserDetails(token);
      }
    }
  }, []);

  return (
    <div id="wrapper" style={styles.container}>
      <LeftMenu />
      <div className="content-page" style={{ overflow: "scroll" }}>
        <div className="content">
          <TopBar />
          {user.userdata !== null && !user.userdata.userdetails.activated ? (
            <Unverified_Email_Banner email={user.userdata.userdetails.email} />
          ) : (
            ""
          )}

          <div className="page-content-wrapper">
            <div className="container-fluid">{children}</div>
          </div>
        </div>
        <footer className="footer">
          © {new Date().getFullYear()} Payperless.com        </footer>
      </div>
    </div>
  );
};

const styles = {
  container: {
    background: "#eff3f6",
    position: "relative",
    // fontFamily: '"Rubik", sans-serif',
    margin: 0
    // fontSize: 15
  }
};

const mapStateToProps = state => ({
  user: state.user
});
const mapDispatchToProps = dispatch => ({
  runSaveBioData: data => {
    dispatch(saveBiodata(data));
  },
  runSaveBankData: data => {
    dispatch(saveBankdata(data));
  },
  runSaveUserData: data => {
    dispatch(saveUserdata(data));
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(Layout);
