import React, { useState, useEffect } from "react";
import Layout from "./layouts/WebsiteLayout";
import { Link } from "react-router-dom";
import axios from "axios";
import MessageSent from "../assets/images/message_sent.svg";

const ForgotPassword = () => {
  const [formdata, setFormdata] = useState({
    email: ""
  });
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState(false);
  const [messageSent, setMessageSent] = useState(false);
  const [outputs, setOutputs] = useState([]);

  const handleChange = e => {
    setFormdata({ ...formdata, [e.target.name]: e.target.value });
  };
  const handleSubmit = async e => {
    e.preventDefault();
    setErrors(false);
    setLoading(true);
    setOutputs([]);
    //validation

    //send data to API
    try {
      const response = await axios.post("/auth/forgot-password", formdata);
      setLoading(false);
      let { errors, success } = response.data;
      if (errors) {
        setMessageSent(false);
        setErrors(true);
        setOutputs(errors);
        return;
      } else if (success);
      {
        setMessageSent(true);
        setOutputs(success);
        return;
      }
    } catch (err) {
      setMessageSent(false);
      setLoading(false);
      setErrors(true);
      setOutputs([{ msg: "An error occured with the server, Try again" }]);
      return;
    }
  };
  return (
    <Layout>
      <section className="login">
        <div className="container">
          <div
            className="row row-eq-height no-gutters sameheight"
            style={{ justifyContent: "center" }}
          >
            <div className="col-lg-6 col-md-12 md-mt-5 ">
              <div className="login-form text-center white-bg box-shadow radius px-5 py-5 makeCenter">
                <div className="social-icons social-colored">
                  <ul className="list-inline">
                    <li className="">
                      <Link to="/">
                        <i className="mdi mdi-layers mdi-48px mdi-payperless-primary"></i>
                      </Link>
                    </li>
                  </ul>
                </div>
                <p className="my-3 font-italic"></p>
                <form id="contact-form" onSubmit={handleSubmit}>
                  <div
                    className="messages"
                    style={{
                      color: errors ? "red" : "#03d665",
                      padding: "20px 0px"
                    }}
                  >
                    {outputs.map((output, key) => (
                      <div key={key}>{output.msg}</div>
                    ))}
                  </div>
                  <div style={{ display: !messageSent ? "block" : "none" }}>
                    <div className="form-group">
                      <input
                        id="form_name"
                        type="email"
                        name="email"
                        onChange={handleChange}
                        value={formdata.email}
                        className="form-control"
                        placeholder="Enter your email to reset password"
                        required="required"
                        data-error="Email is required."
                      />
                      <div className="help-block with-errors"></div>
                    </div>
                    <div className="form-group mt-4 mb-5">
                      <div className="remember-checkbox d-flex align-items-center justify-content-between">
                        <div className="checkbox"></div>
                        <Link to="/login">Login Instead?</Link>
                      </div>
                    </div>{" "}
                    <button
                      className="btn btn-theme text-uppercase"
                      disabled={loading}
                    >
                      <span>Send</span>
                      <div
                        className="lds-ring"
                        style={{ display: loading ? "inline-block" : "none" }}
                      >
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                      </div>
                    </button>
                  </div>
                  <div
                    style={{
                      display: messageSent ? "block" : "none"
                    }}
                  >
                    <img src={MessageSent} style={{ width: "100%" }} />
                  </div>
                </form>
              </div>
            </div>

            <div className="col-lg-4 col-md-12 dark-bg px-4 align-item-middle text-center radius">
              <div>
                <div className="section-title text-white">
                  <h2 className="title" style={{ fontSize: "2rem" }}>
                    Forgot Password
                  </h2>
                  <div className="title-bdr">
                    <div className="left-bdr"></div>
                    <div className="right-bdr"></div>
                  </div>
                  <p>
                    Welcome To Sassaht, Please Enter Your Details And Start
                    Journy With Sassaht
                  </p>
                </div>
                <h5 className="mb-0 mt-4 text-capitalize text-white">
                  New to Payperless? <br /> <Link to="/register">Sign Up</Link>
                </h5>
              </div>
            </div>
          </div>
        </div>
      </section>
    </Layout>
  );
};
export default ForgotPassword;
