import React, { useState, useEffect, Fragment } from "react";
import Layout from "../layouts/Dashboard";
import { connect } from "react-redux";
import { Link, useParams, useHistory } from "react-router-dom";
import axios from "axios";
import Cookies from "js-cookie";
import Loading from "../Loading";
import { addTransactions } from "../../actions/userActions";

const SingleTransaction = ({ user, runAddTransactions }) => {
  let history = useHistory();
  const getUID = () => {
    return Math.floor(Math.random() * 10000);
  };

  const { txref } = useParams();
  const [isloading, setisLoading] = useState(null);
  const [acronym, setAcronym] = useState(null);
  const [transaction, setTransaction] = useState(null);
  const [errors, setErrors] = useState([]);

  const calcInitials = name => {
    let getacronym = name
      .split(/\s/)
      .reduce((response, word) => (response += word.slice(0, 1)), "");
    setAcronym(getacronym);
  };

  const getTransactions = async token => {
    try {
      const getAllTransactions = await axios.get(
        "/account/get/transactions/all",
        {
          headers: { "X-AUTH-TOKEN": token }
        }
      );
      if (getAllTransactions.data.errors) {
        setErrors(getAllTransactions.data.errors);
      } else if (
        getAllTransactions.data.transactions &&
        getAllTransactions.data.status
      ) {
        getSingleTransaction(getAllTransactions.data.transactions);
        runAddTransactions(getAllTransactions.data.transactions);
      }
    } catch (err) {
      getTransactions(token);
    }
  };

  const getSingleTransaction = transactions => {
    let singleTrnx;
    transactions.map(trnx => {
      if (trnx.txref === txref) {
        singleTrnx = trnx;
        return;
      }
    });
    setTransaction(singleTrnx);
  };

  const prettyCurrency = amount => {
    const formatter = new Intl.NumberFormat("en-NG", {
      style: "currency",
      currency: "NGN",
      minimumFractionDigits: 2
    });
    const convertedAmount = formatter.format(amount);
    return convertedAmount;
  };

  useEffect(() => {
    if (Cookies.get("TOKEN") !== null) {
      let token = Cookies.get("TOKEN");
      if (typeof token !== "undefined" && token !== null) {
        //check for invoice in redux store
        if (user.transactions) {
          getSingleTransaction(user.transactions);
        } else {
          //get all invoices
          getTransactions(token);
        }
      }
    }
  }, []);
  // console.clear();
  console.log(transaction);
  return (
    <Layout>
      <div className="row">
        <div className="col-sm-12">
          <div className="page-title-box">
            <div className="btn-group float-right">
              <ol className="breadcrumb hide-phone p-0 m-0">
                <li className="breadcrumb-item active">{txref}</li>
              </ol>
            </div>
            <h4 className="page-title">
              <Link to="/dashboard/transactions">
                <i className="mdi mdi-arrow-left"></i>Back
              </Link>
            </h4>
          </div>
        </div>
      </div>
      {transaction ? (
        <div className="row">
          <div className="col-md-12 col-lg-12 col-xl-6">
            <div className="card m-b-30" style={{}}>
              <div className="card-body">
                <div
                  style={{ display: "flex", justifyContent: "space-between" }}
                >
                  <div className="totalAmount">
                    <div>Amount</div>
                    <h4>{prettyCurrency(transaction.amount)}</h4>
                  </div>
                  <h5
                    className={`header-title mt-0 
                    ${
                      transaction.status === "successful"
                        ? "badge badge-success"
                        : "badge badge-danger"
                    }
                  `}
                    style={{
                      letterSpacing: "0.5px",
                      padding: ".6rem 1.2rem",
                      borderRadius: "1.2rem",
                      alignSelf: "center",
                      justifySelf: "center",
                      fontSize: "1rem"
                    }}
                  >
                    {transaction.status}
                  </h5>
                </div>
                <div id="multi-line-chart" style={{ paddingTop: 20 }}>
                  <div
                    className="row table-responsive additional-items"
                    style={{
                      flexDirection: "column"
                    }}
                  >
                    <table className="table table-hover">
                      <tbody>
                        <tr>
                          <td>Reference</td>
                          <td style={{ textAlign: "right" }}>
                            <b> {transaction.txref}</b>
                          </td>
                        </tr>

                        <tr>
                          <td>Channel </td>
                          <td style={{ textAlign: "right" }}>
                            <b> {transaction.paymenttype}</b>
                          </td>
                        </tr>

                        <tr>
                          <td>Fees</td>
                          <td style={{ textAlign: "right" }}>
                            <b>
                              {" "}
                              {prettyCurrency(transaction.transactionCharge)}
                            </b>
                          </td>
                        </tr>
                        <tr>
                          <td> Settlement Amount</td>
                          <td style={{ textAlign: "right" }}>
                            <b>
                              {" "}
                              {prettyCurrency(transaction.pplamountSettled)}
                            </b>
                          </td>
                        </tr>

                        <tr>
                          <td>Paid on </td>
                          <td style={{ textAlign: "right" }}>
                            <b>
                              {new Date(transaction.created).toDateString()}
                            </b>
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>

                  {/* //end */}
                </div>
              </div>
            </div>
          </div>
          <div className="col-md-12 col-lg-12 col-xl-6">
            <div
              className="m-b-30"
              style={{
                backgroundColor: "transparent!important",
                border: "none!important",
                boxShadow: "none"
              }}
            >
              <div className="card-body">
                <h5 className="header-title mt-0 pb-3">
                  Transaction information
                </h5>

                <div id="morris-donut-chart">
                  <div className="reminder-container">
                    <h6>Customer</h6>
                    <div className="table-responsive">
                      <table className="table table-hover">
                        <tbody>
                          <tr>
                            <td>Email</td>
                            <td style={{ textAlign: "right" }}>
                              <b> {transaction.customerEmail}</b>
                            </td>
                          </tr>

                          {transaction.customerName ? (
                            <tr>
                              <td>Name</td>
                              <td style={{ textAlign: "right" }}>
                                <b> {transaction.customerName}</b>
                              </td>
                            </tr>
                          ) : (
                            ""
                          )}
                          {transaction.customerPhone ? (
                            <tr>
                              <td>Phone</td>
                              <td style={{ textAlign: "right" }}>
                                <b> {transaction.customerPhone}</b>
                              </td>
                            </tr>
                          ) : (
                            ""
                          )}
                          {transaction.ip ? (
                            <tr>
                              <td>IP</td>
                              <td style={{ textAlign: "right" }}>
                                <b> {transaction.ip}</b>
                              </td>
                            </tr>
                          ) : (
                            ""
                          )}
                        </tbody>
                      </table>
                    </div>
                  </div>

                  <div className="transaction-details">
                    <h6>Transaction</h6>
                    <div className="table-responsive">
                      <table className="table table-hover">
                        <tbody>
                          {transaction.txtype ? (
                            <tr>
                              <td>Type</td>
                              <td style={{ textAlign: "right" }}>
                                <b> {transaction.txtype}</b>
                              </td>
                            </tr>
                          ) : (
                            ""
                          )}

                          {transaction.cardType ? (
                            <tr>
                              <td>Card type</td>
                              <td style={{ textAlign: "right" }}>
                                <b> {transaction.cardType}</b>
                              </td>
                            </tr>
                          ) : (
                            ""
                          )}
                          {transaction.narration ? (
                            <tr>
                              <td>Narration</td>
                              <td style={{ textAlign: "right" }}>
                                <b> {transaction.narration}</b>
                              </td>
                            </tr>
                          ) : (
                            ""
                          )}

                          {transaction.status === "successful" ? (
                            <tr>
                              <td>Settlement status</td>
                              <td style={{ textAlign: "right" }}>
                                {transaction.settlementstatus ===
                                "successful" ? (
                                  <span
                                    className="header-title  mt-0 badge badge-success"
                                    style={{
                                      padding: ".6rem 1.2rem",
                                      borderRadius: "1rem",
                                      alignSelf: "center",
                                      justifySelf: "center",
                                      fontSize: ".5rem"
                                    }}
                                  >
                                    {" "}
                                    settled
                                  </span>
                                ) : (
                                  <span
                                    className="header-title mt-0 badge badge-warning"
                                    style={{
                                      padding: ".6rem 1.2rem",
                                      borderRadius: "1rem",
                                      alignSelf: "center",
                                      justifySelf: "center",
                                      fontSize: ".5rem"
                                    }}
                                  >
                                    {" "}
                                    pending
                                  </span>
                                )}
                              </td>
                            </tr>
                          ) : (
                            ""
                          )}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      ) : (
        <Loading />
      )}
    </Layout>
  );
};

const styles = {
  textarea: {
    border: "none",
    resize: "none",
    width: "100%",
    fontWeight: 600,
    fontSize: "1rem"
    // height: 40
  },
  flexDiv: {
    display: "flex",
    flexWrap: "wrap"
  },
  linkDiv: {
    flexGrow: 3
  },
  linkSpan: {
    flexGrow: 1,
    alignSelf: "flex-start",
    backgroundColor: "transparent",
    color: "#000"
  },
  sharelink: {
    marginLeft: 20
  }
};

const mapStateToProps = state => ({
  user: state.user
});
const mapDispatchToProps = dispatch => ({
  runAddTransactions: data => {
    dispatch(addTransactions(data));
  }
});
export default connect(mapStateToProps, mapDispatchToProps)(SingleTransaction);
