import React, { useState, useEffect, Fragment } from "react";
import Layout from "../layouts/Dashboard";
import { connect } from "react-redux";
import { Link, useParams, useHistory } from "react-router-dom";
import axios from "axios";
import Cookies from "js-cookie";

import {
  saveInvoice,
  addInvoiceItem,
  deleteInvoiceItem
} from "../../actions/userActions";
import InvoiceItem from "./InvoiceItem";
import Loading from "../Loading";

const SingleInvoice = ({
  user,
  runSaveInvoice,
  customeremail,
  runAddInvoiceItem,
  runDeleteInvoiceItem
}) => {
  let history = useHistory();
  const getUID = () => {
    return Math.floor(Math.random() * 10000);
  };

  const { txref } = useParams();
  const [invoice, setInvoice] = useState(null);
  const [errors, setErrors] = useState([]);
  const [showMoreCustomerDetails, setShowMoreCustomerDetails] = useState(false);
  const [isloading, setisLoading] = useState(null);
  const [acronym, setAcronym] = useState(null);
  const [invItemRawArray, setInvItemRawArray] = useState([]);
  const [invItem, setInvItem] = useState([]);
  const [runCalcTotalAmount, setRunCalcTotalAmount] = useState(false);
  const [setupComplete, setSetupComplete] = useState(false);

  const initiateInvoiceItem = invoice => {
    // console.log(invoice);
    if (invoice.items.length > 0) {
      // console.clear();
      let existingItemsArray = [];
      // console.log(invoice.items);
      invoice.items.forEach(item => {
        existingItemsArray.push(
          <InvoiceItem
            key={getUID()}
            uid={item.uid}
            txref={txref}
            calcTotal={calcTotalAmount}
            name={item.name}
            quantity={item.quantity}
            unitCost={item.unitCost}
            removeItem={removeInvoiceItem}
          />
        );
      });
      setInvItem([...existingItemsArray]);
    } else {
      setInvItem([
        <InvoiceItem
          key={getUID()}
          uid={getUID()}
          txref={txref}
          calcTotal={calcTotalAmount}
          removeItem={removeInvoiceItem}
        />
      ]);
    }
  };

  const initiateInvoiceItemStore = invoice => {
    if (invoice.items.length > 0) {
      invoice.items.forEach((item, key) => {
        if (key === 0) {
          runAddInvoiceItem(item, item.uid, "new");
        } else {
          runAddInvoiceItem(item, item.uid, "update");
        }
      });
    }
  };

  const [formdata, setFormdata] = useState({
    invoiceNumber: "",
    email: "",
    firstname: "",
    lastname: "",
    phone: "",
    dueDate: "",
    shippingFee: "",
    tax: "",
    discount: "",
    description: "",
    totalAmount: 0,
    subtotal: 0,
    amountBeforeTax: 0,
    taxAmount: 0
  });

  const handleChange = e => {
    setFormdata({ ...formdata, [e.target.name]: e.target.value });
    if (
      e.target.name == "shippingFee" ||
      e.target.name == "tax" ||
      e.target.name == "discount"
    ) {
      setRunCalcTotalAmount(true);
    }
  };

  const saveInvoice = async () => {
    let token = Cookies.get("TOKEN");
    try {
      const resp = await axios.post(
        `/account/edit/invoice/${invoice._id}`,
        {
          ...formdata,
          forwardstatus: "save-only",
          items: user.invoiceItem
        },
        {
          headers: { "X-AUTH-TOKEN": token }
        }
      );
      // console.log(resp.data);
      if (resp.data.errors) {
        setErrors(resp.data.errors);
      } else if (resp.data.status) {
        setSetupComplete(true);
        setisLoading(null);
      }
    } catch (err) {
      saveInvoice();
    }
  };

  const markInvoiceAsPaid = async e => {
    e.preventDefault();
    setisLoading("mark-as-paid");
    let thisBtn = e.target;
    let token = Cookies.get("TOKEN");
    let confirmInvoice = window.confirm(
      "Do you confirm that this invoice has been paid?"
    );
    if (confirmInvoice) {
      try {
        const resp = await axios.post(
          `/account/mark-as-paid/invoice/${invoice._id}`,
          {
            action: "mark-as-paid"
          },
          {
            headers: { "X-AUTH-TOKEN": token }
          }
        );
        setisLoading(null);
        if (resp.data.status) {
          thisBtn.style.backgroundColor = "#03d665";
          thisBtn.style.color = "#fff";
          thisBtn.innerHTML = "Paid!";
        }
      } catch (err) {
        setisLoading(null);
        markInvoiceAsPaid();
      }
    }
  };

  const deleteInvoice = async e => {
    let token = Cookies.get("TOKEN");

    e.preventDefault();
    let confirmDelete = window.confirm(
      "Are you sure you want to delete this invoice"
    );
    if (confirmDelete) {
      setisLoading("delete");
      try {
        const resp = await axios.post(
          `/account/delete/invoice/${invoice._id}`,
          {
            action: "delete"
          },
          {
            headers: { "X-AUTH-TOKEN": token }
          }
        );
        if (resp.data.deletedCount == 1) {
          history.push("/dashboard/invoices");
        }
      } catch (err) {
        deleteInvoice();
      }
    }
  };
  const sendInvoice = async () => {
    let token = Cookies.get("TOKEN");
    try {
      const resp = await axios.post(
        `/account/edit/invoice/${invoice._id}`,
        {
          ...formdata,
          forwardstatus: "save-and-send",
          items: user.invoiceItem
        },
        {
          headers: { "X-AUTH-TOKEN": token }
        }
      );

      //update invoice details in component
      setInvoice({
        ...invoice,
        sentcount: invoice.sentcount + 1,
        lastsent: new Date()
      });
      // console.log(resp.data);
      if (resp.data.errors) {
        setErrors(resp.data.errors);
      } else if (resp.data.status) {
        setSetupComplete(true);
        setisLoading(null);
      }
    } catch (err) {
      sendInvoice();
    }
  };

  const handleSubmit = e => {
    e.preventDefault();
    const btnName = e.target.getAttribute("name");
    if (btnName === "save") {
      setisLoading("save");
      saveInvoice();
    } else if (btnName === "send") {
      setisLoading("send");
      sendInvoice();
    }
  };

  const resendInvoice = e => {
    e.preventDefault();
    setisLoading("send");
    sendInvoice();
  };
  const getInvoices = async token => {
    try {
      const getInvoice = await axios.get("/account/get/invoice/all", {
        headers: { "X-AUTH-TOKEN": token }
      });
      if (getInvoice.data.errors) {
        setErrors(getInvoice.data.errors);
      } else if (getInvoice.data.invoice) {
        runSaveInvoice(getInvoice.data.invoice);
        getSingleInvoice(getInvoice.data.invoice);
      }
    } catch (err) {
      getInvoices(token);
    }
  };

  const getSingleInvoice = invoices => {
    let singleInvoice;
    invoices.map(inv => {
      if (inv.txref === txref) {
        singleInvoice = inv;
        return;
      }
    });
    setInvoice(singleInvoice);

    setInvItemRawArray(singleInvoice.items);
    if (user.userdata) {
      calcInitials(user.userdata.userdetails.name);
    }

    //set invoice item
    initiateInvoiceItem(singleInvoice);
    //set invoice item in redux store
    initiateInvoiceItemStore(singleInvoice);

    //if invoice had previously been filled, autofill formdata
    setFormdata({
      ...formdata,
      invoiceNumber: singleInvoice.invoiceNumber
        ? singleInvoice.invoiceNumber
        : "",
      email:
        singleInvoice.customer && singleInvoice.customer.email
          ? singleInvoice.customer.email
          : "",
      firstname:
        singleInvoice.customer && singleInvoice.customer.firstname
          ? singleInvoice.customer.firstname
          : "",
      lastname:
        singleInvoice.customer && singleInvoice.customer.lastname
          ? singleInvoice.customer.lastname
          : "",
      phone:
        singleInvoice.customer && singleInvoice.customer.phone
          ? singleInvoice.customer.phone
          : "",
      dueDate: singleInvoice.dueDate ? singleInvoice.dueDate : "",
      shippingFee: singleInvoice.shippingFee ? singleInvoice.shippingFee : "",
      tax: singleInvoice.tax ? singleInvoice.tax : "",
      discount: singleInvoice.discount ? singleInvoice.discount : "",
      description: singleInvoice.description ? singleInvoice.description : "",
      totalAmount: singleInvoice.amount ? singleInvoice.amount : ""
    });
    setRunCalcTotalAmount(true);
    // console.log(singleInvoice);

    if (singleInvoice.amount) {
      setSetupComplete(true);
    }
  };

  const displayCustomerDetails = e => {
    e.preventDefault();
    setShowMoreCustomerDetails(true);
  };
  const getRandomColor = () => {
    let colors = [
      "#0064ff",
      "#a257da",
      "#b109aa",
      "#ad0e65",
      "#ad0e0e",
      "#85ad0e",
      "#0ead91",
      "#0e2fad",
      "#0dab57"
    ];
    let randomColor = colors[Math.floor(Math.random() * colors.length)];
    return randomColor;
  };

  const addMoreInvoiceItem = e => {
    e.preventDefault();
    setInvItem([
      ...invItem,
      <InvoiceItem
        uid={getUID()}
        txref={txref}
        calcTotal={calcTotalAmount}
        removeItem={removeInvoiceItem}
      />
    ]);
  };

  const removeInvoiceItem = uid => {
    if (invItemRawArray.length > 0) {
      let remainingItemsArray = [];
      let filteredItemsArray = invItemRawArray.filter(singleItem => {
        return singleItem.uid !== uid;
      });
      setInvItemRawArray(filteredItemsArray);
      // console.log(filteredItemsArray);
      // console.log(invItemRawArray);
      filteredItemsArray.forEach(item => {
        remainingItemsArray.push(
          <InvoiceItem
            key={getUID()}
            uid={item.uid}
            txref={txref}
            calcTotal={calcTotalAmount}
            name={item.name}
            quantity={item.quantity}
            unitCost={item.unitCost}
            removeItem={removeInvoiceItem}
          />
        );
      });
      setInvItem([...remainingItemsArray]);
      runDeleteInvoiceItem(filteredItemsArray);
    }
  };
  // console.log(invItemRawArray);
  const calcInitials = name => {
    let getacronym = name
      .split(/\s/)
      .reduce((response, word) => (response += word.slice(0, 1)), "");
    setAcronym(getacronym);
  };

  const prettyCurrency = amount => {
    const formatter = new Intl.NumberFormat("en-NG", {
      style: "currency",
      currency: "NGN",
      minimumFractionDigits: 2
    });
    const convertedAmount = formatter.format(amount);
    return convertedAmount;
  };

  const calcTotalAmount = () => {
    setRunCalcTotalAmount(true);
  };

  if (runCalcTotalAmount) {
    let itemAmount = 0;
    let discountamount = formdata.discount || 0;
    let shippingFee = formdata.shippingFee || 0;
    let tax = formdata.tax || 0;

    if (user.invoiceItem.length > 0 && user.invoiceItem[0] !== null) {
      user.invoiceItem.map(item => {
        itemAmount = itemAmount + item.unitCost * item.quantity;
      });

      let subtotal = parseFloat(itemAmount),
        //remove discount
        amountBeforeDiscount = parseFloat(itemAmount),
        amountAfterDiscount =
          parseFloat(amountBeforeDiscount) - parseFloat(discountamount),
        //add tax
        amountBeforeTax = parseFloat(amountAfterDiscount),
        taxAmount = (tax / 100) * parseFloat(amountBeforeTax),
        amountAfterTax = parseFloat(amountBeforeTax) + parseFloat(taxAmount),
        //add shipping
        amountBeforeShipping = parseFloat(amountAfterTax),
        amountAfterShipping =
          parseFloat(amountBeforeShipping) + parseFloat(shippingFee),
        //final amount
        totalAmount = parseFloat(amountAfterShipping);
      //return amountBeforeDiscount, amountAfterDiscount, taxAmount,  finalAmount
      setFormdata({
        ...formdata,
        totalAmount,
        subtotal,
        amountBeforeTax,
        taxAmount
      });
      setRunCalcTotalAmount(false);
    }
  }

  const copytext = e => {
    /* Get the text field */
    let copyText = document.getElementById("paymentLink");

    /* Select the text field */
    copyText.select();
    copyText.setSelectionRange(0, 99999); /*For mobile devices*/

    /* Copy the text inside the text field */
    document.execCommand("copy");

    let thisBtn = e.target;
    thisBtn.style.backgroundColor = "#03d665";
    thisBtn.style.color = "#fff";
    thisBtn.innerHTML = "Copied!";
  };
  useEffect(() => {
    if (Cookies.get("TOKEN") !== null) {
      let token = Cookies.get("TOKEN");
      if (typeof token !== "undefined" && token !== null) {
        //check for invoice in redux store
        if (user.invoice) {
          getSingleInvoice(user.invoice);
        } else {
          //get all invoices
          getInvoices(token);
        }
      }
    }

    return () => {
      runAddInvoiceItem(null, "69696", "new");
    };
  }, [user.userdata]);
  // console.clear();
  return (
    <Layout>
      <div className="row">
        <div className="col-sm-12">
          <div className="page-title-box">
            <div className="btn-group float-right">
              <ol className="breadcrumb hide-phone p-0 m-0">
                <li className="breadcrumb-item active">{txref}</li>
              </ol>
            </div>
            <h4 className="page-title">
              <Link to="/dashboard/invoices">
                <i className="mdi mdi-arrow-left"></i>Back
              </Link>
            </h4>
          </div>
        </div>
      </div>

      <div className="row">
        <div className="col-md-12 col-lg-12 col-xl-7">
          <div className="card m-b-30" style={{ minHeight: "100vh" }}>
            <div className="card-body">
              <div style={{ display: "flex", justifyContent: "space-between" }}>
                <div
                  className="receiver-initials"
                  style={{
                    width: 60,
                    height: 60,
                    borderRadius: "50%",
                    background: getRandomColor(),
                    color: "#fff",
                    fontSize: 22,
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center"
                  }}
                >
                  {acronym ? acronym.toUpperCase() : ""}
                </div>
                <h5
                  className="header-title pb-3 mt-0"
                  style={{ letterSpacing: "0.5px" }}
                >
                  Invoice #{formdata.invoiceNumber}
                </h5>
              </div>
              <div id="multi-line-chart" style={{ paddingTop: 20 }}>
                <div className="mid-section row">
                  <div className="col-sm-6">
                    <div id="from">
                      <span className="addr" style={{ fontSize: 16 }}>
                        From:{" "}
                      </span>
                      <span
                        className="title cusdetails"
                        style={{ fontSize: 18 }}
                      >
                        {user.userdata ? user.userdata.userdetails.name : ""}
                      </span>
                    </div>
                    <div className="to">
                      <span className="addr" style={{ fontSize: 16 }}>
                        To:{" "}
                      </span>
                      <span
                        className="title cusdetails"
                        style={{ fontSize: 18 }}
                      >
                        {formdata.email}
                      </span>
                    </div>
                  </div>
                  <div className="col-sm-6">
                    <div className="duedate" style={{ textAlign: "right" }}>
                      <span className="addr">Invoice date: </span>
                      <span className="title cusdetails">
                        {invoice
                          ? // ? new Date(invoice.created).toLocaleDateString()
                            new Date(invoice.created).toDateString()
                          : new Date().toDateString()}
                      </span>
                    </div>
                    <div className="duedate" style={{ textAlign: "right" }}>
                      <span className="addr">Due date: </span>
                      <span className="title cusdetails">
                        {formdata.dueDate !== ""
                          ? // ? new Date(formdata.dueDate).toLocaleDateString()
                            new Date(formdata.dueDate).toDateString()
                          : ""}
                      </span>
                    </div>
                  </div>
                </div>
                <br />

                <div className="add-item-row row">
                  <form action="" style={{ width: "100%" }}>
                    <div className="table-responsive">
                      <table className="table">
                        <thead>
                          <tr>
                            <th style={{ width: "50%" }}>Item name</th>
                            <th style={{ width: "20%" }}>Quantity</th>
                            <th style={{ width: "30%" }}>Unit Cost (NGN)</th>
                          </tr>
                        </thead>

                        <tbody id="invoice-body">{invItem}</tbody>
                      </table>
                      <span
                        className="add-more-btn"
                        style={{
                          fontSize: "12",
                          marginTop: 10
                        }}
                      >
                        <a href="#" onClick={addMoreInvoiceItem}>
                          <i className="mdi mdi-plus"></i>
                          Add another item
                        </a>
                      </span>
                    </div>
                  </form>
                </div>
                <br />
                <div
                  className="row table-responsive additional-items"
                  style={{
                    flexDirection: "column"
                  }}
                >
                  <table className="table table-hover">
                    <tbody>
                      {formdata.subtotal ? (
                        <tr>
                          <td>
                            <b>Subtotal</b>{" "}
                          </td>
                          <td style={{ textAlign: "right" }}>
                            <b> {prettyCurrency(formdata.subtotal)}</b>
                          </td>
                        </tr>
                      ) : (
                        ""
                      )}

                      {formdata.discount ? (
                        <tr>
                          <td>Discount </td>
                          <td style={{ textAlign: "right" }}>
                            {prettyCurrency(formdata.discount)}
                          </td>
                        </tr>
                      ) : (
                        ""
                      )}

                      {formdata.tax ? (
                        <tr>
                          <td>Before Tax </td>
                          <td style={{ textAlign: "right" }}>
                            {prettyCurrency(formdata.amountBeforeTax)}
                          </td>
                        </tr>
                      ) : (
                        ""
                      )}
                      {formdata.tax ? (
                        <tr>
                          <td>Tax </td>
                          <td style={{ textAlign: "right" }}>
                            {formdata.tax}%
                            {formdata.taxAmount
                              ? "(" + prettyCurrency(formdata.taxAmount) + ")"
                              : null}
                          </td>
                        </tr>
                      ) : (
                        ""
                      )}
                      {formdata.shippingFee ? (
                        <tr>
                          <td>Shipping Fee </td>
                          <td style={{ textAlign: "right" }}>
                            {prettyCurrency(formdata.shippingFee)}
                          </td>
                        </tr>
                      ) : (
                        ""
                      )}
                      {invoice ? (
                        <tr>
                          <td>
                            <b>Total</b>{" "}
                          </td>
                          <td style={{ textAlign: "right" }}>
                            <b>{prettyCurrency(formdata.totalAmount)}</b>
                          </td>
                        </tr>
                      ) : (
                        ""
                      )}
                    </tbody>
                  </table>
                </div>

                {formdata.description ? (
                  <div className="notes_div" style={{ paddingBottom: 20 }}>
                    <span style={{ fontSize: 14, textDecoration: "underline" }}>
                      Note
                    </span>
                    <p style={{ color: "#000" }}>{formdata.description}</p>
                  </div>
                ) : (
                  ""
                )}

                {/* //end */}
              </div>
            </div>
          </div>
        </div>
        <div className="col-md-12 col-lg-12 col-xl-5">
          <div
            className="m-b-30"
            style={{
              backgroundColor: "transparent!important",
              border: "none!important",
              boxShadow: "none"
            }}
          >
            <div className="card-body">
              <h5 className="header-title mt-0 pb-3">Set up Invoice</h5>

              <div id="morris-donut-chart">
                {invoice ? (
                  <>
                    {!setupComplete ? (
                      <form id="contact-form">
                        <div
                          className="messages"
                          style={{
                            display: errors.length > 1 ? "block" : "none",
                            color: errors ? "red" : "#03d665",
                            padding: "20px 0px"
                          }}
                        >
                          {/* {errors.map((output, key) => (
                      <div key={key}>{output.msg}</div>
                    ))} */}
                        </div>

                        <div>
                          <div className="form-group row invoiceForm">
                            <label htmlFor="invoiceNumber" className="col-sm-4">
                              Invoice No.
                            </label>
                            <input
                              id="invoiceNumber"
                              type="number"
                              name="invoiceNumber"
                              onChange={handleChange}
                              value={formdata.invoiceNumber}
                              className="form-control col-sm-8"
                              placeholder=""
                            />
                          </div>

                          <div className="form-group row invoiceForm">
                            <label
                              htmlFor="customer-email"
                              className="col-sm-4"
                              style={{ display: "flex", alignItems: "center" }}
                            >
                              Customer
                            </label>

                            <div className="col-sm-8" style={{ padding: 0 }}>
                              <input
                                id="customer-email"
                                type="email"
                                name="email"
                                onChange={handleChange}
                                value={formdata.email}
                                className="form-control customer-email"
                                placeholder="Customer Email"
                              />

                              <small>
                                <a
                                  href="#"
                                  onClick={displayCustomerDetails}
                                  style={{
                                    display: showMoreCustomerDetails
                                      ? "none"
                                      : "block"
                                  }}
                                >
                                  Add more customer details
                                </a>
                              </small>
                            </div>
                          </div>

                          <div
                            className="more-customer-details"
                            style={{
                              display: showMoreCustomerDetails
                                ? "block"
                                : "none"
                            }}
                          >
                            <div className="form-group row invoiceForm">
                              <label htmlFor="firstname" className="col-sm-4">
                                First Name
                              </label>
                              <input
                                id="firstname"
                                type="text"
                                name="firstname"
                                onChange={handleChange}
                                value={formdata.firstname}
                                className="form-control col-sm-8"
                                placeholder=""
                              />
                            </div>

                            <div className="form-group row invoiceForm">
                              <label htmlFor="lastname" className="col-sm-4">
                                Last Name
                              </label>
                              <input
                                id="lastname"
                                type="text"
                                name="lastname"
                                onChange={handleChange}
                                value={formdata.lastname}
                                className="form-control col-sm-8"
                                placeholder=""
                              />
                            </div>

                            <div className="form-group row invoiceForm">
                              <label htmlFor="phone" className="col-sm-4">
                                Phone
                              </label>
                              <input
                                id="phone"
                                type="tel"
                                name="phone"
                                onChange={handleChange}
                                value={formdata.phone}
                                className="form-control col-sm-8"
                                placeholder=""
                              />
                            </div>
                          </div>
                          {/* end hidden */}

                          <div className="form-group row invoiceForm">
                            <label htmlFor="duedate" className="col-sm-4">
                              Due date
                            </label>
                            <input
                              id="duedate"
                              type="date"
                              name="dueDate"
                              onChange={handleChange}
                              min={`${new Date().getFullYear()}-${new Date().getMonth() +
                                1}-${new Date().getDate()}`}
                              value={`${new Date(
                                formdata.dueDate
                              ).getFullYear()}-${new Date(
                                formdata.dueDate
                              ).getMonth() + 1}-${new Date(
                                formdata.dueDate
                              ).getDate()}`}
                              className="form-control col-sm-8"
                              placeholder=""
                            />
                          </div>
                          {invoice.invoiceType === "professional" ? (
                            <Fragment>
                              <div className="form-group row invoiceForm">
                                <label htmlFor="discount" className="col-sm-4">
                                  Discount (NGN)
                                </label>
                                <input
                                  id="discount"
                                  type="number"
                                  name="discount"
                                  onChange={handleChange}
                                  value={formdata.discount}
                                  className="form-control col-sm-8"
                                  placeholder=""
                                />
                              </div>
                              <div className="form-group row invoiceForm">
                                <label htmlFor="tax" className="col-sm-4">
                                  Tax (%)
                                </label>
                                <input
                                  id="tax"
                                  type="number"
                                  name="tax"
                                  onChange={handleChange}
                                  value={formdata.tax}
                                  className="form-control col-sm-8"
                                  placeholder=""
                                  maxLength="3"
                                />
                              </div>
                              <div className="form-group row invoiceForm">
                                <label htmlFor="shipping" className="col-sm-4">
                                  Shipping Fee (NGN)
                                </label>
                                <input
                                  id="shipping"
                                  type="number"
                                  name="shippingFee"
                                  onChange={handleChange}
                                  value={formdata.shippingFee}
                                  className="form-control col-sm-8"
                                  placeholder=""
                                />
                              </div>
                            </Fragment>
                          ) : (
                            ""
                          )}
                          <div className="form-group row invoiceForm">
                            <label htmlFor="description" className="col-sm-4">
                              Note
                            </label>
                            <textarea
                              className="form-control col-sm-8"
                              name="description"
                              id="description"
                              cols="30"
                              rows="2"
                              onChange={handleChange}
                              value={formdata.description}
                              style={{ resize: "none", height: "unset" }}
                              placeholder="Additional Information (optional)"
                            ></textarea>
                          </div>
                          <div className="clearfix"></div>
                          <div
                            className="form-group"
                            style={{
                              display: "flex",
                              justifyContent: "space-between"
                            }}
                          >
                            <button
                              onClick={handleSubmit}
                              className="btn btn-primary btn-sm "
                              disabled={isloading === "save"}
                            >
                              <span name="save">Save Draft</span>
                              <div
                                className="lds-ring"
                                style={{
                                  display:
                                    isloading === "save"
                                      ? "inline-block"
                                      : "none"
                                }}
                              >
                                <div></div>
                                <div></div>
                                <div></div>
                                <div></div>
                              </div>
                            </button>

                            <button
                              onClick={handleSubmit}
                              className="btn btn-theme btn-sm"
                              disabled={isloading === "send"}
                            >
                              <span name="send">Send Now</span>
                              <div
                                className="lds-ring"
                                style={{
                                  display:
                                    isloading === "send"
                                      ? "inline-block"
                                      : "none"
                                }}
                              >
                                <div></div>
                                <div></div>
                                <div></div>
                                <div></div>
                              </div>
                            </button>
                          </div>
                        </div>
                      </form>
                    ) : (
                      <>
                        <div className="reminder-container">
                          <h6>Reminders</h6>
                          <div>
                            {invoice.sentcount > 0 ? (
                              <>
                                <p>
                                  We've sent out{" "}
                                  <b>
                                    {invoice.sentcount}{" "}
                                    {invoice.sentcount > 1 ? "emails" : "email"}{" "}
                                  </b>{" "}
                                  for this request. The last one was sent on{" "}
                                  <b>
                                    {new Date(invoice.lastsent).toDateString()}.
                                  </b>
                                </p>
                                <button
                                  className="btn btn-theme btn-sm"
                                  onClick={resendInvoice}
                                >
                                  <span>Resend Reminder</span>{" "}
                                  <div
                                    className="lds-ring"
                                    style={{
                                      display:
                                        isloading === "send"
                                          ? "inline-block"
                                          : "none"
                                    }}
                                  >
                                    <div></div>
                                    <div></div>
                                    <div></div>
                                    <div></div>
                                  </div>
                                </button>
                              </>
                            ) : (
                              <>
                                <p>
                                  This invoice has been saved but hasn't been
                                  sent yet.
                                </p>
                                <button
                                  className="btn btn-theme btn-sm"
                                  onClick={resendInvoice}
                                >
                                  <span>Send Invoice</span>
                                  <div
                                    className="lds-ring"
                                    style={{
                                      display:
                                        isloading === "send"
                                          ? "inline-block"
                                          : "none"
                                    }}
                                  >
                                    <div></div>
                                    <div></div>
                                    <div></div>
                                    <div></div>
                                  </div>
                                </button>
                              </>
                            )}
                          </div>
                        </div>
                        <hr />
                        <div className="invoice-link">
                          <h6>Invoice link</h6>
                          <div className="invoice-link-input">
                            <div
                              className="subscribe-form"
                              style={styles.flexDiv}
                            >
                              <h4
                                className="title z-index-1"
                                style={styles.linkDiv}
                              >
                                <textarea
                                  id="paymentLink"
                                  rows="1"
                                  readOnly
                                  style={styles.textarea}
                                  value={invoice.paymentlink}
                                ></textarea>
                              </h4>
                              <span
                                className="btn btn-sm btn-theme"
                                onClick={copytext}
                                style={styles.linkSpan}
                              >
                                Copy link
                              </span>
                            </div>
                          </div>
                        </div>
                        <hr />
                        <div className="other-actions">
                          <button
                            className="btn btn-default btn-sm"
                            onClick={() => setSetupComplete(false)}
                          >
                            <i className="mdi mdi-border-color"></i> Edit
                          </button>
                          {!invoice.paymentstatus ? (
                            <button
                              className="btn btn-default btn-sm"
                              onClick={markInvoiceAsPaid}
                            >
                              <i className="mdi mdi-check"></i>
                              <span>Mark as Paid</span>
                              <div
                                className="lds-ring"
                                style={{
                                  display:
                                    isloading === "mark-as-paid"
                                      ? "inline-block"
                                      : "none"
                                }}
                              >
                                <div></div>
                                <div></div>
                                <div></div>
                                <div></div>
                              </div>
                            </button>
                          ) : (
                            ""
                          )}
                          <button
                            className="btn btn-default btn-sm"
                            onClick={deleteInvoice}
                          >
                            <i className="mdi mdi-delete"></i>
                            <span>Delete</span>
                            <div
                              className="lds-ring"
                              style={{
                                display:
                                  isloading === "delete"
                                    ? "inline-block"
                                    : "none"
                              }}
                            >
                              <div></div>
                              <div></div>
                              <div></div>
                              <div></div>
                            </div>
                          </button>
                        </div>
                        {/* end */}
                      </>
                    )}
                  </>
                ) : (
                  <Loading />
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

const styles = {
  textarea: {
    border: "none",
    resize: "none",
    width: "100%",
    fontWeight: 600,
    fontSize: "1rem"
    // height: 40
  },
  flexDiv: {
    display: "flex",
    flexWrap: "wrap"
  },
  linkDiv: {
    flexGrow: 3
  },
  linkSpan: {
    flexGrow: 1,
    alignSelf: "flex-start",
    backgroundColor: "transparent",
    color: "#000"
  },
  sharelink: {
    marginLeft: 20
  }
};

const mapStateToProps = state => ({
  user: state.user
});

const mapDispatchToProps = dispatch => ({
  runSaveInvoice: data => {
    dispatch(saveInvoice(data));
  },
  runAddInvoiceItem: (item, uid, status) => {
    dispatch(addInvoiceItem(item, uid, status));
  },
  runDeleteInvoiceItem: item => {
    dispatch(deleteInvoiceItem(item));
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(SingleInvoice);
