import React, { useState, useEffect } from "react";
import Layout from "./layouts/WebsiteLayout";
import axios from "axios";
import { Link, Redirect, useParams } from "react-router-dom";
import reset_success from "../assets/images/password_reset_successful.svg";

const Resetpassword = () => {
  let { code } = useParams();
  const [formdata, setFormdata] = useState({
    password: "",
    confirmpassword: ""
  });
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState(false);
  const [expiredLink, setExpiredLink] = useState(false);
  const [resetDone, setResetDone] = useState(false);
  const [outputs, setOutputs] = useState([]);
  const [user, setUser] = useState({});

  useEffect(() => {
    //get user details
    const getUserData = async () => {
      try {
        const userDetails = await axios.get(`/auth/reset-password/${code}`);
        setLoading(false);
        let { errors, success } = userDetails.data;
        if (errors) {
          setLoading(false);
          setErrors(true);
          setOutputs(errors);
          return;
        } else if (success) {
          //Check if link is expired
          const expiry = new Date(success[0].resetDetails.expiry);
          if (new Date() > expiry) {
            setLoading(false);
            setErrors(true);
            setExpiredLink(true);
            setOutputs([
              {
                msg:
                  "This link is expired, Kindly click the button to get a new link "
              }
            ]);
            return;
          }
          setUser(success[0].resetDetails);
        }
      } catch (err) {
        setLoading(false);
        setErrors(true);
        setOutputs([
          { msg: "An error occured with the server, Reload this page " }
        ]);
        return;
      }
    };
    getUserData();
  }, []);
  const handleChange = e => {
    setFormdata({ ...formdata, [e.target.name]: e.target.value });
  };

  const handleSubmit = async e => {
    e.preventDefault();
    setErrors(false);
    setOutputs([]);
    setLoading(true);
    //get data
    //validation

    //send data to API
    const response = await axios.post(`/auth/reset-password/${code}`, formdata);
    setLoading(false);
    let { errors, success } = response.data;
    if (errors) {
      setErrors(true);
      setOutputs(errors);
      return;
    } else if (success);
    {
      setResetDone(true);
      setOutputs(success);
    }
  };
  return (
    <Layout>
      <section className="login">
        <div className="container">
          <div className="row row-eq-height no-gutters sameheight">
            <div className="col-lg-4 col-md-12 ml-auto dark-bg px-4 align-item-middle text-center radius ">
              <div>
                <div className="section-title text-white">
                  <h2 className="title" style={{ fontSize: "2rem" }}>
                    Reset Password
                  </h2>
                  <div className="title-bdr">
                    <div className="left-bdr"></div>
                    <div className="right-bdr"></div>
                  </div>
                  <p>
                    Welcome To Sassaht, Please Enter Your Details And Start
                    Journy With Sassaht
                  </p>
                </div>
                <h5 className="mb-0 mt-4 text-capitalize text-white">
                  Have an Account? <br /> <Link to="/login">Sign In</Link>
                </h5>
              </div>
            </div>
            <div className="col-lg-6 col-md-12 mr-auto md-mt-5">
              <div className="login-form text-center white-bg box-shadow radius px-5 py-5 makeCenter">
                <div className="social-icons social-colored">
                  <ul className="list-inline">
                    <li className="">
                      <Link to="/">
                        <i className="mdi mdi-layers mdi-48px mdi-payperless-primary"></i>
                      </Link>
                    </li>
                  </ul>
                </div>
                <p className="my-3 font-italic"></p>
                <form id="contact-form" onSubmit={handleSubmit}>
                  <div
                    className="messages"
                    style={{
                      color: errors ? "red" : "#03d665",
                      padding: "20px 0px"
                    }}
                  >
                    {outputs.map((output, key) => (
                      <div key={key}>{output.msg}</div>
                    ))}
                  </div>
                  <div
                    style={{
                      display:
                        Object.keys(user).length > 0 &&
                        !expiredLink &&
                        !resetDone
                          ? "block"
                          : "none"
                    }}
                  >
                    <div className="form-group">
                      <input
                        id="form_password"
                        type="password"
                        name="password"
                        onChange={handleChange}
                        value={formdata.password}
                        className="form-control"
                        placeholder="Enter a new password"
                        required="required"
                      />
                    </div>
                    <div className="form-group">
                      <input
                        id="form_confirmpassword"
                        type="password"
                        name="confirmpassword"
                        onChange={handleChange}
                        value={formdata.confirmpassword}
                        className="form-control"
                        placeholder="Enter password again"
                        required="required"
                      />
                    </div>
                    <div className="form-group mt-4 mb-5">
                      <div className="remember-checkbox d-flex align-items-center justify-content-between">
                        <div className="checkbox"></div>
                      </div>
                    </div>{" "}
                    <button className="btn btn-theme text-uppercase">
                      <span>Reset Password</span>
                      <div
                        className="lds-ring"
                        style={{ display: loading ? "inline-block" : "none" }}
                      >
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                      </div>
                    </button>
                  </div>
                  <div
                    style={{
                      display:
                        Object.keys(user).length <= 0 &&
                        !expiredLink &&
                        !resetDone
                          ? "block"
                          : "none"
                    }}
                  >
                    <button
                      className="btn btn-theme text-uppercase"
                      onClick={() => {
                        window.location.reload();
                      }}
                    >
                      <span>Reload</span>
                    </button>
                  </div>
                  <div
                    style={{
                      display: expiredLink && !resetDone ? "block" : "none"
                    }}
                  >
                    <Link to="/forgot-password">
                      <button className="btn btn-theme text-uppercase">
                        <span>Get a new link</span>
                      </button>
                    </Link>
                  </div>
                  <div
                    style={{
                      display: resetDone ? "block" : "none"
                    }}
                  >
                    <img src={reset_success} style={{ width: "100%" }} />
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </section>
    </Layout>
  );
};

export default Resetpassword;
