import React, { useEffect, useState } from "react";
import axios from "axios";
import "../receipt.css";
import Layout from "./layouts/WebsiteLayout";
import { useParams } from "react-router-dom";
import Loading from "./Loading";
const Receipt = () => {
  const { txref } = useParams();
  const [errors, setErrors] = useState([]);
  const [payment, setPayment] = useState(null);
  const [receiver, setReceiver] = useState(null);
  const [transaction, setTransaction] = useState(null);

  const getPayment = async (url) => {
    try {
      const transaction = await axios.get(
       url
      );
      if (transaction.data.status) {
        setPayment(transaction.data.transaction);
        getReceiver(transaction.data.transaction.receiverid);
        getTransaction(transaction.data.transaction.receiverid, txref);
      } else if (transaction.data.errors) {
        setErrors(transaction.data.errors);
      }
    } catch (err) {
      getPayment();
    }
  };

  const getReceiver = async receiverid => {
    try {
      const transaction = await axios.get(
        `/account/payment/get/user/${receiverid}`
      );
      if (transaction.data.status) {
        setReceiver(transaction.data.userdetails);
      } else if (transaction.data.errors) {
        setErrors(transaction.data.errors);
      }
    } catch (err) {
      getReceiver();
    }
  };

  const getTransaction = async (receiverid, txref) => {
    try {
      const transactionDetails = await axios.get(
        `/account/transaction/details/${receiverid}/${txref}`
      );
      if (transactionDetails.data.status) {
        setTransaction(transactionDetails.data.transactionDetails);
      } else if (transactionDetails.data.errors) {
        setErrors(transaction.data.errors);
      }
    } catch (err) {
      getTransaction(receiverid, txref);
    }
  };

  const prettyCurrency = amount => {
    const formatter = new Intl.NumberFormat("en-NG", {
      style: "currency",
      currency: "NGN",
      minimumFractionDigits: 2
    });
    const convertedAmount = formatter.format(amount);
    return convertedAmount;
  };

  const makeTitleCase = str => {
    return str
      .toLowerCase()
      .split(" ")
      .map(word => {
        return word.charAt(0).toUpperCase() + word.slice(1);
      })
      .join(" ");
  };
  useEffect(() => {
      console.log(txref)
      let sub = txref.substr(0, 3), url;
      if(sub === "PPL"){
          url = `/pay/payment-link-callback/${txref}`
      }else if(sub === "INV"){
          url = `/pay-invoice/payment-redirect/${txref}`
      }

    getPayment(url);
  }, []);
  return (
    <Layout>
      <table
        className="email-wrapper"
        width="100%"
        cellPadding="0"
        cellSpacing="0"
        role="presentation"
      >
        <tbody>
          <tr>
            <td align="center">
              <table
                className="email-content"
                width="100%"
                cellPadding="0"
                cellSpacing="0"
                role="presentation"
              >
                <tbody>
                  <tr>
                    <td className="email-masthead">
                      <a
                        href="https://payperless.com"
                        className="f-fallback email-masthead_name"
                      >
                        Payperless
                      </a>
                    </td>
                  </tr>
                  <tr>
                    <td
                      className="email-body"
                      width="570"
                      cellPadding="0"
                      cellSpacing="0"
                    >
                      <table
                        className="email-body_inner"
                        align="center"
                        width="570"
                        cellPadding="0"
                        cellSpacing="0"
                        role="presentation"
                      >
                        <tbody>
                          <tr className="liner"></tr>
                          {receiver && payment && transaction ? (
                            <>
                              <tr>
                              <td className="content-cell">
                        <div className="f-fallback">
                          <h1>{
                            transaction.txtype == "invoice" ? 
                            (
                              payment.customer.firstname
                                ? makeTitleCase(
                                    payment.customer.firstname
                                  )
                                : ""
                              )
                            :
                            (
                            payment.senderfirstname
                              ? makeTitleCase(
                                  payment.senderfirstname
                                )
                              : ""
                            )
                            
                          }</h1>
                          <p>Thanks for using Payperless. This is a receipt for your payment on {new Date(payment.created).toDateString()} using {
    transaction.cardType
  }.</p>
                          <table className="purchase" width="100%" cellPadding="0" cellSpacing="0" role="presentation">
                           
                            <tr>
                              <td>
                                <h3 style={{fontWeight:200}}>Reference</h3></td>
                              <td>
                                <h3 className="align-right">{
                                  transaction.txid
                                }</h3></td>
                            </tr>

                            <tr>
                              <td>
                                <h3 style={{fontWeight:200}}>Date</h3></td>
                              <td>
                                <h3 className="align-right">{new Date(payment.created).toLocaleString()}</h3></td>
                            </tr>

                            <tr>
                              <td>
                                <h3 style={{fontWeight:200}}>Paid to</h3></td>
                              <td>
                                <h3 className="align-right">{makeTitleCase(
                                  receiver.name
                                )}</h3></td>
                            </tr>

                            <tr>
                              <td>
                                <h3 style={{fontWeight:200}}>Status</h3></td>
                              <td>
                                <h3 className="align-right">
                                Successful
                                </h3></td>
                            </tr>

                            <tr>
                              <td colSpan="2">
                                <table className="purchase_content" width="100%" cellPadding="0" cellSpacing="0">
                                  <tr>
                                    <th className="purchase_heading" align="left">
                                      <p className="f-fallback">Description</p>
                                    </th>
                                    <th style={{textAlign:"right"}} className="purchase_heading" align="right">
                                      <p className="f-fallback">Amount(NGN)</p>
                                    </th>
                                  </tr>
                                  
                                  <tr>
                                    <td width="60%" className="purchase_item"><span className="f-fallback">{
                                      transaction.narration
                                    }</span></td>
                                    <td style={{textAlign:"right"}} className="align-right" width="40%" className="purchase_item"><span className="f-fallback">{prettyCurrency(
                                      payment.amount
                                    )}</span></td>
                                  </tr>
                                 
                                  <tr>
                                    <td width="60%" className="purchase_footer" valign="middle">
                                      <p className="f-fallback purchase_total purchase_total--label">Total</p>
                                    </td>
                                    <td width="40%" className="purchase_footer" valign="middle">
                                      <p className="f-fallback purchase_total">{prettyCurrency(
                                        payment.amount
                                      )}</p>
                                    </td>
                                  </tr>
                                </table>
                              </td>
                            </tr>
                          </table>


                          <p>If you have any questions about this receipt, simply reach out to our <a href="http://payperless.com/support">support team</a> for help.</p>
                          <table className="body-action" align="center" width="100%" cellpadding="0" cellspacing="0" role="presentation">
                            <tr>
                              <td align="center">
                              
                                <table width="100%" border="0" cellspacing="0" cellpadding="0" role="presentation">
                                  <tr>
                                    <td align="center">
                                      <a href="#"  onClick={(e)=>{e.preventDefault();window.print()}} className="f-fallback button button--green" style={{color:"#fff"}} target="_blank">Print</a>
                                    </td>
                                  </tr>
                                </table>

                              </td>
                            </tr>
                          </table>

                        </div>
                    </td>
                    </tr>
                {/* </table>
                </td>
              </tr> */}
              <tr>
                <td>
                <table style={{position:"relative"}} className="email-footer" align="center" width="570" cellpadding="0" cellspacing="0" role="presentation">
                    <tr>
                    <td className="content-cell" align="center">
                        <p style={{width:"80%"}} className="f-fallback sub align-center">&copy; {new Date().getFullYear()} Payperless.com. All rights reserved.
                        <br/>62 Ordinace road, Trans-Amadi Port Harcourt
                        <br/>Rivers state, Nigeria.
                        </p>
                    </td>
                    </tr>
                </table>
                </td>
                                
                              </tr>
                            </>
                          ) : (
                            
                            <Loading />
                         
                          )}
                        </tbody>
                      </table>
                    </td>
                  </tr>
                </tbody>
              </table>
            </td>
          </tr>
        </tbody>
      </table>
    </Layout>
  );
};
export default Receipt;
