import React, { useState } from "react";
import { connect } from "react-redux";

import { addInvoiceItem } from "../../actions/userActions";

const InvoiceItem = ({
  uid,
  txref,
  runAddInvoiceItem,
  user,
  name = "",
  quantity = "",
  unitCost = "",
  calcTotal,
  removeItem
}) => {
  const [updating, setUpdating] = useState(false);
  const [calculatingAmount, setCalculatingAmount] = useState(false);
  const [item, setItem] = useState({
    uid,
    name,
    quantity,
    unitCost
  });

  const saveToRedux = () => {
    if (user.invoiceItem.length > 0 && user.invoiceItem[0] !== null) {
      let itemIsPresent = false;
      user.invoiceItem.map(singleItem => {
        if (singleItem.uid === uid) {
          itemIsPresent = true;
          return;
        }
      });
      if (itemIsPresent) {
        runAddInvoiceItem(item, uid, "edit");
      } else {
        runAddInvoiceItem(item, uid, "update");
      }
    } else {
      runAddInvoiceItem(item, uid, "new");
    }
    setUpdating(false);
    setCalculatingAmount(true);
  };
  const handleChange = async e => {
    await setItem({
      ...item,
      [e.target.name]: e.target.value
    });

    //save to redux store by setting updating to true
    setUpdating(true);
  };
  if (updating) saveToRedux();
  if (calculatingAmount) calcTotal();

  return (
    <tr style={{ position: "relative" }}>
      <td style={{ borderTop: "none", padding: "5px 12px" }}>
        <input
          type="text"
          className="form-control"
          placeholder="Invoice Item"
          onChange={handleChange}
          uid={uid}
          name="name"
          value={item.name}
          required
          autoFocus
        />
      </td>
      <td style={{ borderTop: "none", padding: "5px 12px" }}>
        <input
          type="number"
          className="form-control"
          placeholder="Quantity"
          onChange={handleChange}
          uid={uid}
          name="quantity"
          value={item.quantity}
          required
        />
      </td>
      <td style={{ borderTop: "none", padding: "5px 12px" }}>
        {" "}
        <input
          type="number"
          className="form-control"
          placeholder="Cost (NGN)"
          onChange={handleChange}
          uid={uid}
          name="unitCost"
          value={item.unitCost}
          required
        />
      </td>
      <td
        className="remove"
        style={styles.closeSpan}
        onClick={() => removeItem(uid)}
      >
        <i
          className="mdi mdi-close close-item"
          style={styles.removeBtn}
          id={uid}
          onClick={e => {
            e.preventDefault();
            removeItem(uid);
          }}
        ></i>
      </td>
    </tr>
  );
};

const styles = {
  closeSpan: {
    height: 50,
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    border: "none"
  },
  removeBtn: {
    position: "absolute",
    // top: "50%",
    // transform: "translateY(-50%)",
    right: 10,
    background: "rgb(239, 236, 236)",
    borderRadius: "50%",
    color: "#000",
    padding: "2px",
    cursor: "pointer",
    boxShadow: "0px .5px  5px #000",
    fontSize: "0.4rem"
  }
};
const mapStateToProps = state => ({
  user: state.user
});

const mapDispatchToProps = dispatch => ({
  runAddInvoiceItem: (item, uid, status) => {
    dispatch(addInvoiceItem(item, uid, status));
  }
});
export default connect(mapStateToProps, mapDispatchToProps)(InvoiceItem);
