import React, { Fragment, useState, useEffect } from "react";
import axios from "axios";
import { useParams } from "react-router-dom";
import Layout from "./layouts/WebsiteLayout";
import Header from "./Home/Header";
import LoadingComponent from "./Loading";

const PayWithInvoice = () => {
  let { invoicetxref } = useParams();
  const [fetchingInvoice, setFetchingInvoice] = useState(false);
  const [fetchingReceiver, setFetchingReceiver] = useState(false);
  const [isInvoiceValid, setisInvoiceValid] = useState(null);
  const [isReceiverValid, setisReceiverValid] = useState(null);
  const [errors, setErrors] = useState([]);
  const [receiver, setReceiver] = useState(null);
  const [invoice, setInvoice] = useState(null);
  const [acronym, setAcronym] = useState(null);
  const [storePicsumImage, setStorePicsumImage] = useState(null);
  const [randomColor, setRandomColor] = useState(null);
  const [paying, setPaying] = useState(false);
  const [redirecting, setRedirecting] = useState(false);

  const fetchReceiver = async id => {
    setFetchingReceiver(true);
    try {
      const getReceiver = await axios.get(`/pay-invoice/getReceiver/${id}`);
      setFetchingReceiver(false);
      if (getReceiver.data.receiver) {
        setisReceiverValid(true);
        //set receiver in redux store
        setReceiver(getReceiver.data.receiver);
        calcInitials(getReceiver.data.receiver.name);
      } else if (getReceiver.data.errors) {
        setisReceiverValid(false);
        setErrors(getReceiver.data.errors);
      }
    } catch (err) {
      setFetchingReceiver(false);
      setErrors([
        { msg: "An error occured with the server, Refresh the page." }
      ]);
    }
  };

  const fetchInvoice = async () => {
    setFetchingInvoice(true);
    try {
      const getInvoice = await axios.get(`/pay-invoice/${invoicetxref}`);
      setFetchingInvoice(false);
      if (getInvoice.data.invoice) {
        setisInvoiceValid(true);
        fetchReceiver(getInvoice.data.invoice.receiverid);
        //set receiver in redux store
        console.log(getInvoice.data.invoice);
        setInvoice(getInvoice.data.invoice);
      } else if (getInvoice.data.errors) {
        setisInvoiceValid(false);
        setErrors(getInvoice.data.errors);
      }
    } catch (err) {
      setFetchingInvoice(false);
      setErrors([
        { msg: "An error occured with the server, Refresh the page." }
      ]);
    }
  };

  const calcInitials = name => {
    let getacronym = name
      .split(/\s/)
      .reduce((response, word) => (response += word.slice(0, 1)), "");
    setAcronym(getacronym);
  };

  const prettyCurrency = amount => {
    const formatter = new Intl.NumberFormat("en-NG", {
      style: "currency",
      currency: "NGN",
      minimumFractionDigits: 2
    });
    const convertedAmount = formatter.format(amount);
    return convertedAmount;
  };

  const getRandomColor = () => {
    let colors = [
      "#0064ff",
      "#a257da",
      "#b109aa",
      "#ad0e65",
      "#ad0e0e",
      "#85ad0e",
      "#0ead91",
      "#0e2fad",
      "#0dab57"
    ];
    let randomColor = colors[Math.floor(Math.random() * colors.length)];
    return randomColor;
  };

  const handleSubmit = async e => {
    e.preventDefault();
    setPaying(true);

    try {
      const payURL = await axios.post(`/pay-invoice/${invoicetxref}`, {
        txref: invoicetxref
      });
      console.log(payURL.data);
      setRedirecting(true);
      // window.open(payURL.data.data.link, "_blank").location(); // On a new Tab
      window.location = payURL.data.data.link; // In current Tab
    } catch (err) {
      setPaying(false);
      setErrors([{ msg: "An error occured with the server, Try again." }]);
    }
  };

  useEffect(() => {
    setStorePicsumImage(
      `https://picsum.photos/id/${Math.floor(Math.random() * 1000)}/450/200`
    );
    setRandomColor(getRandomColor());
    //fetch invoice details
    fetchInvoice();
  }, [invoicetxref]);
  return (
    <Layout>
      <Header />
      {/* pricing Table */}

      <section>
        <div className="container">
          {receiver !== null ? (
            <div
              className="row align-items-center no-gutters"
              style={styles.flexCenter}
            >
              <div className="md-mt-5 paymentlink-paydiv">
                <div className="price-table active">
                  <div
                    className="price-header"
                    style={{
                      display: "flex",
                      flexDirection: "column",
                      justifyContent: "center",
                      alignItems: "center"
                    }}
                  >
                    <span
                      className="img-cont"
                      style={{
                        minWidth: 400,
                        minHeight: 100,
                        margin: "-80px -30px 0"
                      }}
                    >
                      <img src={storePicsumImage} alt="" />
                    </span>
                    <div
                      className="receiver-initials"
                      style={{
                        width: 100,
                        height: 100,
                        borderRadius: "50%",
                        background: randomColor,
                        color: "#fff",
                        marginTop: -50,
                        fontSize: 38,
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "center"
                      }}
                    >
                      {acronym ? acronym.toUpperCase() : ""}
                    </div>
                    <h3 className="price-title" style={{ margin: "20px 0 0" }}>
                      {receiver.name}
                    </h3>

                    <div className="price-list">
                      <ul className="list-unstyled">
                        <li>
                          <small>Invoice #{invoice.invoiceNumber}</small>
                        </li>
                      </ul>
                    </div>
                    <Fragment>
                      <div className="price-value">
                        <span>
                          To:{" "}
                          <b>
                            {" "}
                            {invoice.customer.firstname
                              ? invoice.customer.firstname +
                                " " +
                                invoice.customer.lastname
                              : invoice.customer.email}
                          </b>
                        </span>
                        <h2 id="amount-h2">
                          <input
                            type="text"
                            value={
                              invoice.amount
                                ? prettyCurrency(invoice.amount)
                                : ""
                            }
                            // onChange={handleChange}
                            id="amount"
                            name="amount"
                            className="numberNoScroll"
                            style={styles.numberInput}
                            dir="ltr"
                            autoComplete="off"
                            placeholder="0.00"
                            readOnly
                          />
                        </h2>
                        <span>
                          {invoice.description ? invoice.description : null}
                        </span>
                      </div>

                      <button
                        className="btn btn-theme btn-circle mt-4"
                        disabled={paying}
                        onClick={handleSubmit}
                      >
                        <span>
                          {paying ? (
                            <Fragment>
                              {redirecting ? "Redirecting" : "Paying"}{" "}
                              <div className="lds-ring">
                                <div></div>
                                <div></div>
                                <div></div>
                                <div></div>
                              </div>
                            </Fragment>
                          ) : (
                            "Pay Now"
                          )}
                        </span>
                      </button>
                    </Fragment>
                  </div>
                </div>
              </div>
            </div>
          ) : (
            <div style={styles.flexCenter} className="md-mt-5">
              <LoadingComponent />
            </div>
          )}
        </div>
      </section>

      {/* pricing Table */}
    </Layout>
  );
};
const styles = {
  flexCenter: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    align: "center"
  },
  numberInput: {
    width: 280,
    border: "none",
    textAlign: "center"
  }
};

export default PayWithInvoice;
