import React, { useState, useEffect } from "react";
import Layout from "../layouts/Dashboard";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { withRouter } from "react-router-dom";

const Settings = ({ children, location }) => {
  let path = location.pathname.split("/")[3];

  return (
    <Layout>
      <div className="settings-container" style={styles.container}>
        <ul style={styles.ul}>
          <li style={path === "profile" ? styles.liactive : styles.li}>
            <Link
              style={path === "profile" ? styles.li_active : styles.li_a}
              to="/dashboard/settings/profile"
            >
              Profile
            </Link>
          </li>
          <li style={path === "business" ? styles.liactive : styles.li}>
            <Link
              style={path === "business" ? styles.li_active : styles.li_a}
              to="/dashboard/settings/business"
            >
              Business
            </Link>
          </li>
          <li style={path === "bank-accounts" ? styles.liactive : styles.li}>
            <Link
              style={path === "bank-accounts" ? styles.li_active : styles.li_a}
              to="/dashboard/settings/bank-accounts"
            >
              Bank Accounts
            </Link>
          </li>
          <li style={path === "preferences" ? styles.liactive : styles.li}>
            <Link
              style={path === "preferences" ? styles.li_active : styles.li_a}
              to="/dashboard/settings/preferences"
            >
              Preferences
            </Link>
          </li>
        </ul>
      </div>
      {children}
    </Layout>
  );
};

const styles = {
  container: {
    padding: "30px 0 0"
  },
  ul: {
    display: "flex",
    // justifyContent: "space-around",
    alignItems: "center",
    fontSize: 16,
    listStyle: "none",
    color: "#ccc"
  },
  li: {
    padding: "5px 15px"
  },
  li_a: {
    color: "#2d3b48"
  },
  li_active: {
    color: "#03d665",
    fontWeight: "550"
  },
  liactive: {
    padding: "5px 15px",
    borderBottom: "2.5px solid #03d665"
  }
};
const mapStateToProps = state => ({
  user: state.user
});
export default connect(mapStateToProps)(withRouter(Settings));
