import React, { useEffect, useState } from "react";
import Loading from "../Loading";
import { connect } from "react-redux";
import { Link, useHistory } from "react-router-dom";
import Cookies from "js-cookie";
import axios from "axios";

//Actions
import { saveBiodata, saveUserdata } from "../../actions/userActions";

import ChangePasswordModal from "./ChangePasswordModal";
const Profile = ({ user, runSaveBioData, runSaveUserData }) => {
  const [fetchUserdata, setFetchUserdata] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState([]);
  const [successmsg, setSuccessmsg] = useState(null);
  const [formdata, setFormdata] = useState({
    firstname: "",
    lastname: "",
    email: "",
    phone: ""
  });

  //   address: "3 Mingi Estate"
  // city: "Port-Harcourt"
  // created: "2019-11-30T22:07:14.129Z"
  // firstname: "Queen"
  // lastname: "Opara"
  // modified: "2019-11-30T22:07:14.129Z"
  // phone: "09039951233"
  // state: "Rivers"
  // userid: "5de2e73b9808300f64220143"

  let history = useHistory();
  const getProfile = async token => {
    try {
      const hasProfile = await axios.get("/account/get/biodata", {
        headers: { "X-AUTH-TOKEN": token }
      });

      if (!hasProfile.data.status) {
        history.push("/dashboard/setup-biodata");
      } else {
        console.log(hasProfile.data);
        setFormdata({
          ...formdata,
          firstname: hasProfile.data.biodata.firstname,
          lastname: hasProfile.data.biodata.lastname,
          phone: hasProfile.data.biodata.phone
        });
        //set biodata to state via redux
        runSaveBioData(hasProfile.data);
        setFetchUserdata(true);
      }
    } catch (err) {
      //retry
      getProfile(token);
    }
  };

  const getUserDetails = async token => {
    try {
      const userDetails = await axios.get("/account/get/user", {
        headers: { "X-AUTH-TOKEN": token }
      });

      if (!userDetails.data.status) {
        history.push("/logout");
      } else {
        setFormdata({ ...formdata, email: userDetails.data.userdetails.email });
        runSaveUserData(userDetails.data);
      }
    } catch (err) {
      //retry
      getUserDetails(token);
    }
  };

  const handleSubmit = async e => {
    e.preventDefault();
    setLoading(true);
    try {
      let token = Cookies.get("TOKEN");
      const resp = await axios.post("/account/edit/biodata", formdata, {
        headers: { "X-AUTH-TOKEN": token }
      });
      console.log(resp.data);
      setLoading(false);
      let { errors, status } = resp.data;
      if (errors) {
        setErrors(errors);
        return;
      } else if (status);
      {
        setSuccessmsg(resp.data.msg);
      }
    } catch (err) {
      setLoading(false);
      setErrors([{ msg: "An error occured with the server, Try again." }]);
    }
  };
  const handleChange = e => {
    e.preventDefault();
    setFormdata({ ...formdata, [e.target.name]: e.target.value });
  };

  const runFetchUserData = () => {
    let token = Cookies.get("TOKEN");
    //get and save user details
    if (user.userdata === null) {
      getUserDetails(token);
    } else {
      setFormdata({
        ...formdata,
        email: user.userdata.userdetails.email
      });
    }
    setFetchUserdata(false);
  };
  if (fetchUserdata) runFetchUserData();

  const showModalFtn = e => {
    e.preventDefault();
    setShowModal(true);
  };
  const closeModalFtn = e => {
    e.preventDefault();
    setShowModal(false);
  };
  useEffect(() => {
    if (Cookies.get("TOKEN") !== null) {
      let token = Cookies.get("TOKEN");

      if (typeof token !== "undefined" && token !== null) {
        //has User created a profile?
        if (user.biodata === null) {
          getProfile(token);
        } else {
          setFormdata({
            ...formdata,
            firstname: user.biodata.biodata.firstname,
            lastname: user.biodata.biodata.lastname,
            phone: user.biodata.biodata.phone
          });
          setFetchUserdata(true);
        }
      }
    }
  }, []);
  return (
    <>
      <div className="row">
        <div className="col-md-12 col-lg-12 col-xl-12">
          <div className="card m-b-30">
            <div className="card-body">
              <div
                className="invoice-top"
                style={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "flex-start"
                }}
              >
                <h5 className="header-title pb-3 mt-0">Personal Information</h5>
                {/* <button className="btn btn-sm btn-theme" onClick={handleSubmit}>
                  <i className="mdi mdi-save"></i>
                  Save
                </button> */}
              </div>
              <hr />
              <div id="multi-line-chart" style={{ minHeight: 400 }}>
                {showModal ? (
                  <ChangePasswordModal closeModal={closeModalFtn} />
                ) : (
                  ""
                )}

                {user.userdata ? (
                  <div className="personal-info-form">
                    {/* {user.userdata.userdetails.name} */}
                    <form name="personal" onSubmit={handleSubmit}>
                      {errors.length > 0 ? (
                        <div
                          className="messages  alert alert-danger"
                          style={{
                            padding: "20px"
                          }}
                        >
                          {errors.map((error, key) => (
                            <div key={key}>{error.msg}</div>
                          ))}
                        </div>
                      ) : null}

                      {successmsg ? (
                        <div
                          className="messages alert alert-success"
                          style={{
                            padding: "20px"
                          }}
                        >
                          <div>{successmsg}</div>
                        </div>
                      ) : null}

                      <div className="form-group" style={{ display: "flex" }}>
                        <div className="col-sm-6">
                          <label htmlFor="firstname">First name</label>
                          <input
                            type="text"
                            id="firstname"
                            className="form-control"
                            name="firstname"
                            value={formdata.firstname}
                            onChange={handleChange}
                            required
                          />
                        </div>
                        <div className="col-sm-6">
                          <label htmlFor="lastname">Last name</label>
                          <input
                            type="text"
                            id="lastname"
                            className="form-control"
                            name="lastname"
                            value={formdata.lastname}
                            onChange={handleChange}
                            required
                          />
                        </div>
                      </div>
                      <div className="form-group" style={{ display: "flex" }}>
                        <div className="col-sm-6">
                          <label htmlFor="email">Email</label>
                          <input
                            type="email"
                            id="email"
                            className="form-control"
                            readOnly
                            value={formdata.email}
                          />
                        </div>
                        <div className="col-sm-6">
                          <label htmlFor="phone">Phone</label>
                          <input
                            type="tel"
                            id="phone"
                            name="phone"
                            className="form-control"
                            value={formdata.phone}
                            onChange={handleChange}
                            required
                          />
                        </div>
                      </div>

                      <button
                        type="submit"
                        className="btn float-right btn-theme btn-sm text-uppercase ml-2 pt30 pb30"
                        disabled={loading}
                      >
                        {!loading ? "Save" : "Saving..."}
                        <div
                          className="lds-ring"
                          style={{ display: loading ? "inline-block" : "none" }}
                        >
                          <div></div>
                          <div></div>
                          <div></div>
                          <div></div>
                        </div>
                      </button>
                      <div className="clearfix"></div>
                      <hr />
                      <div
                        className="form-group"
                        style={{ padding: "30px 15px" }}
                      >
                        <label htmlFor="passwordchange">
                          Change Your Password
                        </label>
                        <br />
                        <button
                          onClick={showModalFtn}
                          className="btn btn-default btn-md"
                        >
                          Change Password
                        </button>
                      </div>
                    </form>
                  </div>
                ) : (
                  <Loading />
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

const mapStateToProps = state => ({
  user: state.user
});
const mapDispatchToProps = dispatch => ({
  runSaveBioData: data => {
    dispatch(saveBiodata(data));
  },
  runSaveUserData: data => {
    dispatch(saveUserdata(data));
  }
});

export default connect(mapStateToProps, mapDispatchToProps)(Profile);
