import React, { useState } from "react";
import Cookies from "js-cookie";
import axios from "axios";

export default function ChangePasswordModal({ closeModal }) {
  const [isloading, setIsloading] = useState(false);
  const [successmsg, setSuccessmsg] = useState(null);
  const [errors, setErrors] = useState([]);
  const [formdata, setFormdata] = useState({
    oldpassword: "",
    password: "",
    confirmpassword: ""
  });
  const handleSubmit = async e => {
    e.preventDefault();
    setIsloading(true);
    // console.log(formdata);
    try {
      let token = Cookies.get("TOKEN");
      const resp = await axios.post("/auth/change-password", formdata, {
        headers: { "X-AUTH-TOKEN": token }
      });
      //   console.log(resp.data);
      setIsloading(false);
      let { errors, status } = resp.data;
      if (errors) {
        setErrors(errors);
        return;
      } else if (status);
      {
        setSuccessmsg(resp.data.msg);
      }
    } catch (err) {
      setIsloading(false);
      setErrors([{ msg: "An error occured with the server, Try again." }]);
    }
  };

  const handleChange = e => {
    e.preventDefault();
    setFormdata({ ...formdata, [e.target.name]: e.target.value });
  };
  return (
    <>
      <div
        className="modal fade show bs-example-modal-center"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="mySmallModalLabel"
        aria-hidden="true"
        style={{ display: "block", paddingRight: 17 }}
      >
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <form className="form">
              <div className="modal-header">
                <h5 className="modal-title" id="exampleModalLabel">
                  Change Password
                </h5>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-label="Close"
                  onClick={closeModal}
                >
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>

              <div className="modal-body">
                <div className="container-fluid">
                  {errors.length > 0 ? (
                    <div
                      className="messages  alert alert-danger"
                      style={{
                        padding: "20px"
                      }}
                    >
                      {errors.map((error, key) => (
                        <div key={key}>{error.msg}</div>
                      ))}
                    </div>
                  ) : null}

                  {successmsg ? (
                    <div
                      className="messages alert alert-success"
                      style={{
                        padding: "20px"
                      }}
                    >
                      <div>{successmsg}</div>
                    </div>
                  ) : null}
                  <div className="form-group row">
                    <div
                      className="col-md-12 invoiceTypeContainer"
                      style={{ display: "flex", flexDirection: "column" }}
                    >
                      <div className="form-group">
                        <label htmlFor="oldpassword">Old Password</label>
                        <input
                          type="password"
                          name="oldpassword"
                          id="oldpassword"
                          className="form-control"
                          placeholder="********"
                          onChange={handleChange}
                          required
                        />
                      </div>
                      <div className="form-group">
                        <label htmlFor="newpassword">New Password</label>
                        <input
                          type="password"
                          name="password"
                          id="newpassword"
                          className="form-control"
                          placeholder="********"
                          onChange={handleChange}
                          required
                        />
                      </div>
                      <div className="form-group">
                        <label htmlFor="newpassword2">Retype Password</label>
                        <input
                          type="password"
                          name="confirmpassword"
                          id="newpassword2"
                          className="form-control"
                          placeholder="********"
                          onChange={handleChange}
                          required
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="modal-footer">
                <button
                  onClick={handleSubmit}
                  className="btn btn-theme btn-sm "
                  disabled={isloading}
                >
                  <span name="save">{!isloading ? "Save" : "Saving..."}</span>
                  <div
                    className="lds-ring"
                    style={{
                      display: isloading ? "inline-block" : "none"
                    }}
                  >
                    <div></div>
                    <div></div>
                    <div></div>
                    <div></div>
                  </div>
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
      <div className="modal-backdrop fade show"></div>
    </>
  );
}
