import React, { useState, useEffect } from "react";
import Layout from "../layouts/Dashboard";
import axios from "axios";
import Cookies from "js-cookie";
import { Link, useHistory } from "react-router-dom";
import LoadingComponent from "../Loading";

const BankDetails = () => {
  let history = useHistory();
  const [formData, setFormData] = useState({
    bankcode: "",
    accountname: "",
    accountnumber: "",
    isprimary: true
  });
  const [loading, setLoading] = useState(false);
  const [allBanksArray, setAllBanksArray] = useState(false);
  const [errors, setErrors] = useState(false);

  const getAccountName = async (acctnum, bankcode) => {
    try {
      const accountDetails = await axios.post("/account/verifyaccount", {
        accountnumber: acctnum,
        bankcode: bankcode
      });
      if (accountDetails.data.data.data.responsecode == 0) {
        setFormData({
          ...formData,
          bankcode,
          accountnumber: acctnum,
          accountname: accountDetails.data.data.data.accountname
        });
      } else {
        setErrors([{ msg: accountDetails.data.data.data.responsemessage }]);
        setFormData({
          ...formData,
          bankcode,
          accountnumber: acctnum,
          accountname: ""
        });
      }
    } catch (err) {
      setErrors([{ msg: "Unable to fetch Account name... Reload the page" }]);
    }
  };

  const handleChange = async e => {
    e.preventDefault();
    setErrors([]);
    setFormData({ ...formData, [e.target.name]: e.target.value });
    if (e.target.name == "accountnumber" && e.target.value.length == 10) {
      let acctnum = e.target.value;
      setFormData({
        ...formData,
        accountnumber: acctnum,
        accountname: "Fetching Account name... Please wait"
      });
      getAccountName(acctnum, formData.bankcode);
    } else if (
      e.target.name == "bankcode" &&
      formData.accountnumber.length == 10
    ) {
      setFormData({
        ...formData,
        bankcode: e.target.value,
        accountname: "Fetching Account name... Please wait"
      });
      getAccountName(formData.accountnumber, e.target.value);
    }
  };

  const handleSubmit = async e => {
    e.preventDefault();
    setErrors([]);
    setLoading(true);
    //get data
    //validation

    try {
      let token = Cookies.get("TOKEN");
      //send data to API
      console.log(formData);
      const response = await axios.post("/account/create/bank", formData, {
        headers: { "X-AUTH-TOKEN": token }
      });
      setLoading(false);
      let { errors, data } = response.data;
      if (errors) {
        setErrors(errors);
        return;
      } else if (data !== "" && data !== null);
      {
        history.push("/dashboard/setup-complete");
      }
    } catch (err) {
      setLoading(false);
      setErrors([{ msg: "An error occured with the server, Try again." }]);
    }
  };

  const validateToken = async token => {
    const isTokenValid = await axios.get("/auth/validate-token", {
      headers: { "X-AUTH-TOKEN": token }
    });
    if (!isTokenValid.data.status) {
      history.push("/logout");
    }
  };
  const getAllBanks = async () => {
    try {
      const allbanks = await axios.get("/account/allbanks");
      console.log(allbanks.data);
      if (allbanks.data.errors) {
        setErrors(allbanks.data.errors);
      } else {
        setAllBanksArray(allbanks.data);
      }
    } catch (error) {
      getAllBanks();
    }
  };

  const getProfile = async token => {
    try {
      const hasBank = await axios.get("/account/get/bankdetails", {
        headers: { "X-AUTH-TOKEN": token }
      });
      if (!hasBank.data.status) {
        return;
      } else {
        history.push("/dashboard");
      }
    } catch (err) {
      //retry
      getProfile(token);
    }
  };

  useEffect(() => {
    if (Cookies.get("TOKEN") !== null) {
      let token = Cookies.get("TOKEN");

      if (typeof token !== "undefined" && token !== null) {
        //has User created a profile?
        getProfile(token);
      }
      //get all banks
      getAllBanks();
    }
  }, []);

  return (
    <Layout>
      <div className="container-fluid">
        <div className="row">
          <div className="col-sm-12">
            <div className="page-title-box">
              <div className="btn-group float-right">
                <ol className="breadcrumb hide-phone p-0 m-0">
                  <li className="breadcrumb-item">
                    <Link to="/dashboard">Dashboard</Link>
                  </li>
                  <li className="breadcrumb-item active">Setup Bank Details</li>
                </ol>
              </div>
              <h4 className="page-title">Setup Bank Details</h4>
            </div>
          </div>
        </div>

        <div className="row">
          <div className="col-12">
            <div className="card m-b-30">
              <div className="card-body">
                <h4 className="mt-0 header-title">Bank Details</h4>

                <form className="" onSubmit={handleSubmit}>
                  {errors ? (
                    <div
                      className="messages"
                      style={{
                        color: "red",
                        padding: "20px 0px"
                      }}
                    >
                      {errors.map((error, key) => (
                        <div key={key}>{error.msg}</div>
                      ))}
                    </div>
                  ) : null}
                  {allBanksArray.length > 0 ? (
                    <>
                      <div className="form-group row">
                        <div className="col-sm-6">
                          <select
                            className="form-control"
                            name="bankcode"
                            value={formData.bankcode}
                            onChange={handleChange}
                          >
                            <option>Select Bank</option>
                            {allBanksArray.map((singleBank, key) => (
                              <option key={key} value={singleBank.Code}>
                                {singleBank.Name}
                              </option>
                            ))}
                          </select>
                        </div>

                        <div className="col-sm-6">
                          <input
                            className="form-control"
                            type="tel"
                            placeholder="Account number"
                            name="accountnumber"
                            value={formData.accountnumber}
                            onChange={handleChange}
                            maxLength={10}
                          />
                        </div>
                      </div>
                      <div className="form-group row">
                        <div className="col-sm-12">
                          <input
                            className="form-control"
                            type="text"
                            placeholder="Account Name"
                            name="accountname"
                            value={formData.accountname}
                            readOnly
                          />
                        </div>
                      </div>
                      <button
                        type="submit"
                        className="btn float-right btn-theme text-uppercase ml-2"
                        disabled={loading}
                      >
                        Save
                        <div
                          className="lds-ring"
                          style={{ display: loading ? "inline-block" : "none" }}
                        >
                          <div></div>
                          <div></div>
                          <div></div>
                          <div></div>
                        </div>
                      </button>
                    </>
                  ) : (
                    <>{!errors ? <LoadingComponent /> : ""}</>
                  )}
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default BankDetails;
