import React from "react";
import Layout from "./layouts/WebsiteLayout";
import { Link } from "react-router-dom";
import img1 from "../assets/images/404/01.png";
import img2 from "../assets/images/404/02.png";
const NotFound = () => (
  <Layout>
    <section className="fullscreen-banner error-page text-center o-hidden p-0 pos-r">
      <div className="align-center">
        <div className="container">
          <div className="row">
            <div className="col-lg-8 col-md-12 ml-auto mr-auto">
              <div className="error-img pos-r mb-5">
                <div className="error-round rotateme">
                  <img className="img-fluid" src={img2} alt="" />
                </div>
                <img className="img-fluid" src={img1} alt="" />
              </div>
            </div>
          </div>
          <div className="row">
            <div className="col-lg-6 col-md-10 ml-auto mr-auto">
              <h5 className="line-h-5">
                You’re either misspelling the URL or requesting a page that's no
                longer here.
              </h5>
              <Link to="/" className="btn btn-theme">
                <span>Back To Home Page</span>
              </Link>
            </div>
          </div>
        </div>
      </div>
    </section>
  </Layout>
);

export default NotFound;
