import React, { useState, useEffect } from "react";
import Layout from "./layouts/WebsiteLayout";
import { Link } from "react-router-dom";
import axios from "axios";
import Cookies from "js-cookie";

const Register = ({ history }) => {
  const [formdata, setFormdata] = useState({
    businessname: "",
    email: "",
    password: ""
  });
  const [revealPassword, setRevealPassword] = useState(false);
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState([]);

  const togglePasswordReveal = () => {
    setRevealPassword(!revealPassword);
  };
  const handleChange = e => {
    setFormdata({ ...formdata, [e.target.name]: e.target.value });
  };

  const handleSubmit = async e => {
    e.preventDefault();
    setErrors([]);
    setLoading(true);
    //validation

    try {
      //send data to backend
      const response = await axios.post("/auth/register", formdata);
      setLoading(false);
      let { errors, token } = response.data;
      if (errors) {
        setErrors(errors);
        return;
      } else if (token !== "" && token !== null);
      {
        Cookies.set("TOKEN", token, { expires: 7 });
        history.push("/dashboard");
      }
    } catch (err) {
      setLoading(false);
      setErrors([{ msg: "An error occured with the server, Try again." }]);
    }
  };
  return (
    <Layout>
      <section className="login">
        <div className="container">
          <div
            className="row row-eq-height no-gutters sameheight"
            style={{ justifyContent: "center" }}
          >
            <div className="col-lg-6 col-md-12 md-mt-5 ">
              <div className="login-form text-center white-bg box-shadow radius px-5 py-5 makeCenter">
                <div className="social-icons social-colored">
                  <ul className="list-inline">
                    <li className="">
                      <Link to="/">
                        <i className="mdi mdi-layers mdi-48px mdi-payperless-primary"></i>
                      </Link>
                    </li>
                  </ul>
                </div>
                <p className="my-3 font-italic"></p>
                <form id="registration-form" onSubmit={handleSubmit}>
                  <div
                    className="messages"
                    style={{
                      color: errors ? "red" : "#000",
                      margin: "20px 0px"
                    }}
                  >
                    {errors.map((error, key) => (
                      <div key={key}>{error.msg}</div>
                    ))}
                  </div>
                  <div className="form-group">
                    <input
                      id="form_name"
                      type="text"
                      name="businessname"
                      onChange={handleChange}
                      value={formdata.businessname}
                      className="form-control"
                      placeholder="Business Name"
                      required="required"
                      data-error="Business name is required."
                    />
                    <div className="help-block with-errors"></div>
                  </div>
                  <div className="form-group">
                    <input
                      id="form_name"
                      type="email"
                      name="email"
                      onChange={handleChange}
                      value={formdata.email}
                      className="form-control"
                      placeholder="Email"
                      required="required"
                      data-error="Email is required."
                    />
                    <div className="help-block with-errors"></div>
                  </div>
                  <div className="form-group" style={styles.passwordInput}>
                    <input
                      id="form_password"
                      type={revealPassword ? "text" : "password"}
                      name="password"
                      onChange={handleChange}
                      value={formdata.password}
                      className="form-control"
                      placeholder="Password"
                      required="required"
                      data-error="password is required."
                    />
                    <i
                      title={`${revealPassword ? "Hide" : "Show"} password`}
                      className={`fa ${
                        revealPassword ? "fa-eye-slash" : "fa-eye"
                      }`}
                      style={styles.toggleReveal}
                      onClick={togglePasswordReveal}
                    ></i>
                    <div className="help-block with-errors"></div>
                  </div>

                  <button
                    className="btn btn-theme text-uppercase"
                    disabled={loading}
                  >
                    <span>Sign Up</span>
                    <div
                      className="lds-ring"
                      style={{ display: loading ? "inline-block" : "none" }}
                    >
                      <div></div>
                      <div></div>
                      <div></div>
                      <div></div>
                    </div>
                  </button>
                </form>
              </div>
            </div>

            <div className="col-lg-4 col-md-12 dark-bg px-4 align-item-middle text-center radius">
              <div>
                <div className="section-title text-white">
                  <h2 className="title">Sign Up</h2>
                  <div className="title-bdr">
                    <div className="left-bdr"></div>
                    <div className="right-bdr"></div>
                  </div>
                  <p>
                    Welcome To Sassaht, Please Enter Your Details And Start
                    Journy With Sassaht
                  </p>
                </div>
                <h5 className="mb-0 mt-4 text-capitalize text-white">
                  Have an account? <br /> <Link to="/login">Login</Link>
                </h5>
              </div>
            </div>
          </div>
        </div>
      </section>
    </Layout>
  );
};

const styles = {
  passwordInput: {
    position: "relative"
  },
  toggleReveal: {
    position: "absolute",
    right: 10,
    top: "50%",
    transform: "translateY(-50%)",
    cursor: "pointer"
  }
};
export default Register;
